import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import axios from 'axios'
import Sidebar from './IndComponents/Sidebar'
import Navbar from './IndComponents/Navbar'

export default function AccountDetail() {
    const { user } = useSelector(state => state.user)
    const [edit, setedit] = useState(false)
    const [newuserdata, setNewUserData] = useState({
        firstName: "",
        lastName: "",
        premisetype: "",
        contact: "",
        address: ""
    })
    const [newpassword,setnewpassword] = useState('')
    const [confirmpassword,setconfirmpassword] = useState('')
    const [currentpassword,setcurrentpassword] = useState('')
    const handleEdit = () => {
        setedit(true)
    }
    const handleSave = async () => {
        try {
            if (newuserdata.address === '') {
                newuserdata.address = user.address
            }
            if (newuserdata.contact === '') newuserdata.contact = user.contact
            if (newuserdata.premisetype === '') newuserdata.premisetype = user.premisetype
            if (newuserdata.firstName === '') newuserdata.firstName = user.firstName
            if (newuserdata.lastName === '') newuserdata.lastName = user.lastName
            const res = await axios.post('/api/individual/updateuserdetails', newuserdata)
            if (res.data.success) {
                setedit(false)
                window.location.reload()
            }
            else {
                window.alert('failed to edit data')
                setedit(false)
            }
        } catch (err) {
            console.log(err)
        }

    }
    let name, value
    const handleonChange = (e) => {
        name = e.target.name
        value = e.target.value
        setNewUserData({ ...newuserdata, [name]: value })
    }

    const handleChangePassword = async ()=>{
        const data = {newpassword,currentpassword,confirmpassword}
        try{
            const res = await axios.post('/api/individual/updatepassword',data)
            if(res.data.success){
                window.alert(res.data.message)
                currentpassword=''
                newpassword=''
                confirmpassword=''
            }
            else{
                window.alert(res.data.message)
            }
        }catch(err){
            console.log(err)
        }
    }
    return (
        <>
            <Navbar />
            <div style={{ display: 'flex' }}>
                <Sidebar />
                <div style={{ width: '84%', height: '88vh', overflowY: 'scroll' }}>
                    {edit ? (
                        <div style={{ display: 'flex', alignItems: 'center', justifyContent: "center", width: "100%", height: "80vh" }}>
                            <div style={{ width: "60%", height: "70vh", padding: "5px 10px", borderRadius: "20px", border: "2px solid" }}>
                                <h1 style={{ fontSize: '30px' }}>Account Details</h1>
                                <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', padding: '1px 10px' }}>
                                    <h2 style={{ fontSize: '20px' }}>User Profile</h2>
                                    <button type="button" class="text-gray-900 bg-white border border-gray-300 focus:outline-none hover:bg-gray-100 focus:ring-4 focus:ring-gray-200 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-gray-800 dark:text-white dark:border-gray-600 dark:hover:bg-gray-700 dark:hover:border-gray-600 dark:focus:ring-gray-700"
                                        style={{ marginRight: "30px" }}
                                        onClick={handleSave}
                                    >save</button>
                                </div>
                                <div style={{ display: "flex", alignItems: "center", marginLeft: "40%" }}>
                                    <img src='https://img.freepik.com/free-vector/businessman-character-avatar-isolated_24877-60111.jpg?w=2000' style={{ width: '100px', height: "100px", borderRadius: '50%' }} />
                                    <button type="button" class="text-gray-900 bg-white border border-gray-300 focus:outline-none hover:bg-gray-100 focus:ring-4 focus:ring-gray-200 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-gray-800 dark:text-white dark:border-gray-600 dark:hover:bg-gray-700 dark:hover:border-gray-600 dark:focus:ring-gray-700"
                                        style={{ marginLeft: "20px", marginTop: "25px", padding: '5px', width: "10rem" }}
                                    ><h6 style={{ fontSize: '12px' }}>Upload</h6><h6 style={{ fontSize: '12px' }}>Profile Picture</h6></button>
                                </div>
                                <div style={{ width: '100%', height: '52vh', display: 'flex', justifyContent: 'center' }}>
                                    <div style={{ width: '80%', height: "50vh", marginTop: "20px", padding: '5px' }}>
                                        <div class="grid grid-cols-2 gap-4">

                                            <div>Email ID</div>
                                            <div>{user?.email}</div>


                                            <div>First Name</div>
                                            <div><input type="text" name='firstName' value={newuserdata.firstName} class="form-control-plaintext" placeholder={user?.firstName} onChange={handleonChange} style={{ width: "20rem", height: "25px", padding: '2px', border: '1px solid' }} /></div>


                                            <div>Last Name</div>
                                            <div><input type="text" name='lastName' value={newuserdata.lastName} class="form-control-plaintext" placeholder={user?.lastName} onChange={handleonChange} style={{ width: "20rem", height: "25px", padding: '2px', border: '1px solid' }} /></div>


                                            <div>Premise Type</div>
                                            <div><input type="text" name='premisetype' value={newuserdata.premisetype} class="form-control-plaintext" placeholder={user?.premisetype} onChange={handleonChange} style={{ width: "20rem", height: "25px", padding: '2px', border: '1px solid' }} /></div>
                                            <div>Contact</div>
                                            <div><input type="text" name='contact' value={newuserdata.contact} class="form-control-plaintext" placeholder={user?.contact} onChange={handleonChange} style={{ width: "20rem", height: "25px", padding: '2px', border: '1px solid' }} /></div>


                                            <div>Address</div>
                                            <div><input type="text" name='address' value={newuserdata.address} class="form-control-plaintext" placeholder={user?.address} onChange={handleonChange} style={{ width: "20rem", height: "25px", padding: '2px', border: '1px solid' }} /></div>
                                        </div>


                                    </div>
                                </div>

                            </div>
                        </div>
                    ) : (
                        <div style={{ display: 'flex', alignItems: 'center', justifyContent: "center", width: "100%", height: "80vh"}}>
                            <div style={{ width: "60%", height: "70vh", padding: "5px 10px", borderRadius: "20px", border: "2px solid" }}>
                                <h1 style={{ fontSize: '30px' }}>Account Details</h1>
                                <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', padding: '1px 10px' }}>
                                    <h2 style={{ fontSize: '20px' }}>User Profile</h2>
                                    <button type="button" class="text-gray-900 bg-white border border-gray-300 focus:outline-none hover:bg-gray-100 focus:ring-4 focus:ring-gray-200 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-gray-800 dark:text-white dark:border-gray-600 dark:hover:bg-gray-700 dark:hover:border-gray-600 dark:focus:ring-gray-700"
                                        style={{ marginRight: "30px" }}
                                        onClick={handleEdit}
                                    >Edit</button>
                                </div>
                                <div style={{ display: "flex", alignItems: "center", marginLeft: "40%" }}>
                                    <img src='https://img.freepik.com/free-vector/businessman-character-avatar-isolated_24877-60111.jpg?w=2000' style={{ width: '100px', height: "100px", borderRadius: '50%' }} />
                                    <button type="button" class="text-gray-900 bg-white border border-gray-300 focus:outline-none hover:bg-gray-100 focus:ring-4 focus:ring-gray-200 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-gray-800 dark:text-white dark:border-gray-600 dark:hover:bg-gray-700 dark:hover:border-gray-600 dark:focus:ring-gray-700"
                                        style={{ marginLeft: "20px", marginTop: "25px", padding: '5px', width: "10rem" }}
                                    ><h6 style={{ fontSize: '12px' }}>Upload</h6><h6 style={{ fontSize: '12px' }}>Profile Picture</h6></button>
                                </div>
                                <div style={{ width: '100%', height: '52vh', display: 'flex', justifyContent: 'center' }}>
                                    <div style={{ width: '80%', height: "50vh", marginTop: "20px", padding: '5px' }}>
                                        <div class="grid grid-cols-2 gap-4" style={{ marginTop: '20px' }}>

                                            <div>Email ID</div>
                                            <div>{user?.email}</div>


                                            <div>First Name</div>
                                            <div>{user?.firstName}</div>


                                            <div>Last Name</div>
                                            <div>{user?.lastName}</div>

                                            <div>Premise Type</div>
                                            <div>{user?.premisetype}</div>


                                            <div>Contact</div>
                                            <div>{user?.contact}</div>


                                            <div>Address</div>
                                            <div>{user?.address}</div>
                                        </div>


                                    </div>
                                </div>

                            </div>
                        </div>
                    )}
                    <div style={{ width: '100%', height: '40vh', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                        <div style={{ width: '60%', height: "40vh", border: "2px solid", borderRadius: "10px", padding: "20px" }}>
                            <h1 style={{ fontSize: "20px" }}>Change Password</h1>
                            <div class="grid grid-cols-2 gap-4" style={{ marginTop: '30px', width: "70%" }}>
                                <div>Current Password</div>
                                <div><input type="text"  value={currentpassword} class="form-control-plaintext"  onChange={(e)=>{setcurrentpassword(e.target.value)}} style={{ width: "20rem", height: "30px", padding: '2px', border: '1px solid' }} /></div>
                                <div>New Password</div>
                                <div><input type="text"  value={newpassword} class="form-control-plaintext"  onChange={(e)=>{setnewpassword(e.target.value)}} style={{ width: "20rem", height: "30px", padding: '2px', border: '1px solid' }} /></div>
                                <div>Confirm Password</div>
                                <div><input type="text"  value={confirmpassword} class="form-control-plaintext"  onChange={(e)=>{setconfirmpassword(e.target.value)}} style={{ width: "20rem", height: "30px", padding: '2px', border: '1px solid' }} /></div>
                            </div>
                            <div style={{width:'100%',display:'flex',justifyContent:"center",marginTop:"30px"}}>
                            <button type="button" onClick={handleChangePassword} class="text-gray-900 bg-white border border-gray-300 focus:outline-none hover:bg-gray-100 focus:ring-4 focus:ring-gray-200 font-medium rounded-full text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-gray-800 dark:text-white dark:border-gray-600 dark:hover:bg-gray-700 dark:hover:border-gray-600 ">Update</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </>
    )
}
