import React from 'react'
import Sidebar from './IndComponents/Sidebar'
import Navbar from './IndComponents/Navbar'
import DatePicker from "react-datepicker";

export default function ScheduleService() {
  return (
    <>
      <Navbar />
      <div style={{ display: 'flex' }}>
        <Sidebar />
        <div style={{ display: 'flex', alignItems: 'center', flexDirection: "column", width: "84%", height: "88vh", overflowY: "scroll" }}>

          <h1 style={{ fontSize: "25px", textDecoration: 'underline', textDecorationThickness: "1.5px", padding: "2px",marginTop:"20px" }}>Raise Your Maintenance Query Ticket</h1>
          <div style={{ width: "90%", marginTop: "10px", display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
            <div class="form-group row" style={{ margin: "7px 5px", width: "50%", display: 'flex' }} >
              <div class="col-sm-5" style={{ marginLeft: 'auto' }}>
                <input type="text" class="form-control-plaintext" id="staticEmail" placeholder="Enter Address" style={{ width: "20rem", height: "40px", padding: '3px', border: '1px solid' }} />
              </div>
            </div>
            <div class="form-group row" style={{ margin: "7px 5px", width: "50%", display: 'flex', justifyContent: 'space-between' }} >
              <label for="staticEmail" class="col-sm-3 col-form-label" style={{ fontSize: "20px", marginTop: '5px' }} >Service Type</label>
              <div class="col-sm-5">
                <select
                  id="city"
                  className="mt-1 p-2 block w-full rounded-md border border-gray-300 focus:ring focus:ring-indigo-200 focus:outline-none focus:border-indigo-400"
                  required
                  style={{ width: '20rem' }}
                  name='city'
                >
                  <option value="">select</option>
                  <option value="New Mexico">Account</option>
                  <option value="Missouri">Payout</option>
                  <option value="Texas">Others</option>
                  <option value="Texas">Complaint</option>
                  <option value="Texas">Question</option>
                </select>
              </div>
            </div>
            <div class="form-group row" style={{ width: "50%", display: 'flex', justifyContent: 'space-between', flexDirection: 'column' }} >
              <label for="staticEmail" class="col-sm-3 col-form-label" style={{ fontSize: "20px", marginTop: '5px' }} >Problem</label>
              <div class="col-sm-5">
                <textarea class="form-control" id="exampleFormControlTextarea1" rows="3" placeholder='Enter Manually' style={{ height: "80px", width: "100%", border: "1px solid" }}></textarea>
              </div>
            </div>
            <div style={{ width: "60%", height: "30vh", marginTop: "30px" }}>
              <h3 style={{ fontSize: "20px", fontWeight: '500', textDecoration: "underline", textDecorationThickness: "1.5px" }}>Preferred Time Slot</h3>
              <div class="grid grid-cols-2 ">
                <div style={{ border: "1px solid", height: '6vh', padding: "5px", fontSize: "18px", fontWeight: "500" }}>Today</div>
                <div style={{ border: "1px solid ", height: '6vh', padding: "5px", fontSize: "18px", fontWeight: '500' }}>Tomorrow</div>
                <div style={{ border: "1px solid ", height: '5vh', padding: "5px", fontSize: "14px" }}>
                  <div class="form-check form-check-inline" style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                    <label class="form-check-label" for="inlineRadio1">Anytime</label>
                    <input class="form-check-input" type="radio" id="inlineRadio1" name='distfromcenter' value="Within 5km" style={{ height: '20px', width: '20px', marginRight: '5px' }} />
                  </div>
                </div>
                <div style={{ border: "1px solid ", height: '5vh', padding: "5px", fontSize: "14px" }}>
                  <div class="form-check form-check-inline" style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                    <label class="form-check-label" for="inlineRadio1">Anytime</label>
                    <input class="form-check-input" type="radio" id="inlineRadio1" name='distfromcenter' value="Within 5km" style={{ height: '20px', width: '20px', marginRight: '5px' }} />
                  </div>
                </div>
                <div style={{ border: "1px solid ", height: '5vh', padding: "5px", fontSize: "14px" }}>
                  <div class="form-check form-check-inline" style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                    <label class="form-check-label" for="inlineRadio1">Now</label>
                    <input class="form-check-input" type="radio" id="inlineRadio1" name='distfromcenter' value="Within 5km" style={{ height: '20px', width: '20px', marginRight: '5px' }} />
                  </div>
                </div>
                <div style={{ border: "1px solid ", height: '5vh', padding: "5px", fontSize: "14px" }}>
                  <div class="form-check form-check-inline" style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                    <label class="form-check-label" for="inlineRadio1">10:00am to 12:00am</label>
                    <input class="form-check-input" type="radio" id="inlineRadio1" name='distfromcenter' value="Within 5km" style={{ height: '20px', width: '20px', marginRight: '5px' }} />
                  </div>
                </div>
                <div style={{ border: "1px solid ", height: '5vh', padding: "5px", fontSize: "14px" }}>
                  <div class="form-check form-check-inline" style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                    <label class="form-check-label" for="inlineRadio1">Within 2 to 3 hours</label>
                    <input class="form-check-input" type="radio" id="inlineRadio1" name='distfromcenter' value="Within 5km" style={{ height: '20px', width: '20px', marginRight: '5px' }} />
                  </div>
                </div>
                <div style={{ border: "1px solid ", height: '5vh', padding: "5px", fontSize: "14px" }}>
                  <div class="form-check form-check-inline" style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                    <label class="form-check-label" for="inlineRadio1">12:00pm to 03:00pm</label>
                    <input class="form-check-input" type="radio" id="inlineRadio1" name='distfromcenter' value="Within 5km" style={{ height: '20px', width: '20px', marginRight: '5px' }} />
                  </div>
                </div>
                <div style={{ border: "1px solid ", height: '5vh', padding: "5px", fontSize: "14px" }}>
                  <div class="form-check form-check-inline" style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                    <label class="form-check-label" for="inlineRadio1">Within 3 to 6 hours</label>
                    <input class="form-check-input" type="radio" id="inlineRadio1" name='distfromcenter' value="Within 5km" style={{ height: '20px', width: '20px', marginRight: '5px' }} />
                  </div>
                </div>
                <div style={{ border: "1px solid ", height: '5vh', padding: "5px", fontSize: "14px" }}>
                  <div class="form-check form-check-inline" style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                    <label class="form-check-label" for="inlineRadio1">03:00pm to 06:00 pm</label>
                    <input class="form-check-input" type="radio" id="inlineRadio1" name='distfromcenter' value="Within 5km" style={{ height: '20px', width: '20px', marginRight: '5px' }} />
                  </div>
                </div>
              </div>
            </div>
            <div style={{ width: '60%', height: "15vh", marginTop: '20px' }}>
              <h3 style={{ fontSize: "20px", fontWeight: '500', textDecoration: "underline", textDecorationThickness: "1.5px" }}>Enter Manually</h3>
              <div class="form-group row" style={{ margin: "7px 5px", width: "70%", display: 'flex', justifyContent: 'space-between', alignItems: "center" }} >
                <label for="staticEmail" class="col-sm-3 col-form-label" style={{ fontSize: "17px", marginTop: '5px' }} >Select Date</label>
                <div style={{ width: "300px", display: 'flex', alignItems: 'center' }}> <DatePicker className='w-96 h-10 p-2.5 shadow appearance-none border rounded  text-gray-700 leading-tight focus:outline-none focus:shadow-outline' dateFormat="dd/MM/yyyy" placeholderText='DD/MM/YYYY' /></div>
              </div>
            </div>
            <div class="form-group row" style={{ width: "60%", display: 'flex', justifyContent: 'space-between', flexDirection: 'column' }} >
                <label for="staticEmail" class="col-sm-3 col-form-label" style={{ fontSize: "20px", marginTop: '5px',textDecoration:'underline' }} >Any Message</label>
                <div class="col-sm-5">
                  <textarea class="form-control" id="exampleFormControlTextarea1" rows="3" placeholder='Upto 100 words only' style={{ height: "130px", width: "100%", border: "1px solid" }}></textarea>
                </div>
              </div>
            <div style={{ marginTop: "20px", width: '50%',height:"15vh", display: 'flex', justifyContent: 'center' }}>
              <button style={{ marginRight: '10px',height:"3rem" }} class="bg-transparent hover:bg-green-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded">
                Submit
              </button>
            </div>
          </div>
        </div>
      </div>

    </>
  )
}