import React, { useState } from 'react';
import './ContactUs.css';
import Navbar from './Navbar';
import Footer from './Footer';
const ContactUs = () => {
  const [formData, setFormData] = useState({
    subject: '',
    queryType: '',
    name: '',
    email: '',
    city: '',
    contact: '',
    description: '',
    file: null,
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    setFormData({
      ...formData,
      file: file,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
  
  };

  return (
      <div className="containervv">
        <Navbar />
    <div className="contact-us-container">
      <h1>Feel free to Contact Us:</h1>

      <form onSubmit={handleSubmit}>
        <label>
          Subject*:
          <input
            type="text"
            name="subject"
            value={formData.subject}
            onChange={handleChange}
            required
          />
        </label>

        <label>
          Query Type*:
          <select
            name="queryType"
            value={formData.queryType}
            onChange={handleChange}
            required
          >
            <option value="">Select</option>
            <option value="Account">Account</option>
            <option value="Payment">Payment</option>
            <option value="Service">Service</option>
            <option value="Sales">Sales</option>
            <option value="Complaint">Complaint</option>
            <option value="Other">Other</option>
          </select>
        </label>

        <label>
          Name*:<br></br>
          <input
            type="text"
            name="name"
            value={formData.name}
            onChange={handleChange}
            required
          />
        </label>

        <label>
          Email ID*:
          <input
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            required
          />
        </label>

        <label>
          City*:<br></br>
          <input
            type="text"
            name="city"
            value={formData.city}
            onChange={handleChange}
            required
          />
        </label>

        <label>
          Contact*:
          <input
            type="text"
            name="contact"
            value={formData.contact}
            onChange={handleChange}
            required
          />
        </label>

        <label>
          Description*:
          <textarea
          className='descript'  name="description"
            value={formData.description}
            onChange={handleChange}
            required
          />
        </label>

        <label>
          + Attach a file:
          <input
            type="file"
            name="file"
            accept=".pdf, .doc, .docx"
            onChange={handleFileChange}
          />
        </label>

        <button type="submit" className="submit-button0">Submit</button>
      </form>

      <div className="contact-info">
        <p className='contact'>Email At: info@fixtechcare.com</p>
        <p className='contact'>Helpline: +912269621753</p>
        <p className='contact'>[10:00 am to 06:00 pm] I.S.T</p>
      </div>
      </div>
      <Footer />
    </div>
  );
};

export default ContactUs;

