import React, { useState } from 'react';
import './Career.css';

import Navbar from './Navbar';
import Footer from './Footer';

const Career = () => {
  const [formData, setFormData] = useState({
    country: '',
    province: '',
    city: '',
    positionAppliedAs: '',
    positionDepartment: '',
    positionType: '',
    name: '',
    gender: '',
    day: '',
    month: '',
    year: '',
    email: '',
    contactNumber: '',
    expectedCTC: '',
    experienceYears: '',
    resume: null,
    captcha: '',
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    setFormData({
      ...formData,
      resume: file,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const emailBody = `
      Country: ${formData.country}
      Province: ${formData.province}
      City: ${formData.city}
      Position Applied As: ${formData.positionAppliedAs}
      Position Department: ${formData.positionDepartment}
      Position Type: ${formData.positionType}
      Name: ${formData.name}
      Gender: ${formData.gender}
      Date of Birth: ${formData.day}/${formData.month}/${formData.year}
      Email: ${formData.email}
      Contact Number: ${formData.contactNumber}
      Expected CTC: ${formData.expectedCTC}
      Experience in Years: ${formData.experienceYears}
      Captcha: ${formData.captcha}
    `;

    // Create a mailto link to send the form data to the email address
    window.location.href = `mailto:career@fixtechcare.com?subject=Job Application&body=${encodeURIComponent(emailBody)}`;
  };

  return (
    <div className="containervv">
      <Navbar />
      <div className="career-container">
        <h1 className="career">Please Fill in the below details</h1>
        <p className="info">(*) Marked Fields are mandatory</p>

        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="country">COUNTRY APPLYING FOR*:</label>
            <select
              id="country"
              name="country"
              value={formData.country}
              onChange={handleChange}
              required
            >
              <option value="">Select</option>
              <option value="Asia Pacific">Asia Pacific</option>
              <option value="Europe">Europe</option>
              <option value="North America">North America</option>
            </select>
          </div>

          <div className="form-group">
          <label htmlFor="province">PROVINCE*:</label>
          <select
            id="province"
            name="province"
            value={formData.province}
            onChange={handleChange}
            required
          >
            <option value="">Select</option>
            <option value="India">India</option>   
            <option value="Singapore">Singapore</option> 
              <option value="Thailand">Thailand</option>  
               <option value="Australia">Australia</option>
            <option value="New Zealand">New Zealand</option>
            <option value="UAE (United Arab Emirates)">UAE (United Arab Emirates)</option>
            <option value="England">England</option>
<option value="Lazio">Lazio</option>
<option value="Lombardy">Lombardy</option>
<option value="Geneva">Geneva</option>
<option value="Zurich">Zurich</option>
<option value="Hamburg">Hamburg</option>
<option value="Berlin">Berlin</option>
<option value="Ontario">Ontario</option>
<option value="Quebec">Quebec</option>
<option value="British Columbia">British Columbia</option>
<option value="Alberta">Alberta</option>
<option value="Saskatchewan">Saskatchewan</option>
<option value="Manitoba">Manitoba</option>
<option value="New Brunswick">New Brunswick</option>
<option value="Nova Scotia">Nova Scotia</option>
<option value="Prince Edward Island">Prince Edward Island</option>
<option value="Newfoundland and Labrador">Newfoundland and Labrador</option>
<option value="New York">New York</option>
<option value="California">California</option>
<option value="Illinois">Illinois</option>
<option value="Texas">Texas</option>
<option value="Arizona">Arizona</option>
<option value="Pennsylvania">Pennsylvania</option>
<option value="Florida">Florida</option>
<option value="Georgia">Georgia</option>
<option value="North Carolina">North Carolina</option>
<option value="Ohio">Ohio</option>
<option value="Washington, D.C.">Washington, D.C.</option>
<option value="South Carolina">South Carolina</option>
<option value="Michigan">Michigan</option>
<option value="Wisconsin">Wisconsin</option>
<option value="Canada">Canada</option>
<option value="United States of America">United States of America</option>
  {/* Add province options */}
          </select>
        </div>

        <div className="form-group">
          <label htmlFor="city">CITY*:<br></br></label>
          <select
            id="city"
            name="city"
            value={formData.city}
            onChange={handleChange}
            required
          >
            <option value="">Select</option>
            <option value="Pune">Pune</option>
<option value="Mumbai">Mumbai</option>
<option value="Navi Mumbai">Navi Mumbai</option>
<option value="Bengaluru">Bengaluru</option>
<option value="Gurugram">Gurugram</option>
<option value="Ahmedabad">Ahmedabad</option>
<option value="Hyderabad">Hyderabad</option>
<option value="Chennai">Chennai</option>
<option value="Kolkata">Kolkata</option>
<option value="Singapore">Singapore</option>
<option value="Thailand">Thailand</option>
<option value="Australia">Australia</option>
<option value="New Zealand">New Zealand</option>
<option value="Hong Kong">Hong Kong</option>
<option value="United Arab Emirates">United Arab Emirates</option>
<option value="London">London</option>
<option value="Manchester">Manchester</option>
<option value="Birmingham">Birmingham</option>
<option value="Rome">Rome</option>
<option value="Milan">Milan</option>
<option value="Geneva">Geneva</option>
<option value="Zurich">Zurich</option>
<option value="Hamburg">Hamburg</option>
<option value="Berlin">Berlin</option>
<option value="Toronto">Toronto</option>
<option value="Montreal">Montreal</option>
<option value="Vancouver">Vancouver</option>
<option value="Calgary">Calgary</option>
<option value="Edmonton">Edmonton</option>
<option value="Ottawa">Ottawa</option>
<option value="New York">New York</option>
<option value="Los Angeles">Los Angeles</option>
<option value="Chicago">Chicago</option>
<option value="Houston">Houston</option>
<option value="Phoenix">Phoenix</option>
<option value="Philadelphia">Philadelphia</option>
<option value="San Antonio">San Antonio</option>
<option value="San Diego">San Diego</option>
<option value="Dallas">Dallas</option>
<option value="Austin">Austin</option>
<option value="Atlanta">Atlanta</option>
<option value="Jacksonville">Jacksonville</option>
<option value="Washington">Washington</option>
<option value="Columbus">Columbus</option>
<option value="Charlotte">Charlotte</option>
<option value="Hong Kong">Hong Kong</option>

            {/* Add city options */}
          </select>
        </div>

        <div className="form-group">
          <label htmlFor="positionAppliedAs">POSITION APPLIED AS*:</label>
          <select
            id="positionAppliedAs"
            name="positionAppliedAs"
            value={formData.positionAppliedAs}
            onChange={handleChange}
            required
          >
            <option value="">Select Position As</option>
            <option value="Fresher">Fresher</option>
            <option value="Experienced">Experienced</option>
          </select>
        </div>

        <div className="form-group">
          <label htmlFor="positionDepartment">POSITION APPLIED FOR*:</label>
          <select
            id="positionDepartment"
            name="positionDepartment"
            value={formData.positionDepartment}
            onChange={handleChange}
            required
          >
            <option value="">Select Department</option>
            <option value="Marketing">Marketing</option>
            <option value="Sales">Sales</option>
            <option value="Human Resource">Human Resource</option>
            <option value="Technology">Technology</option>
            <option value="Back Operations">Back Operations</option>
            <option value="Administration">Administration</option>
            <option value="Management">Management</option>
          </select>
        </div>

        <div className="form-group">
          <label htmlFor="positionType">POSITION TYPE*:</label>
          <select
            id="positionType"
            name="positionType"
            value={formData.positionType}
            onChange={handleChange}
            required
          >
            <option value="">Select Position Type</option>
            <option value="Full Time">Full Time</option>
            <option value="Part Time">Part Time</option>
            <option value="Contract">Contract</option>
          </select>
        </div>

        <div className="form-group">
          <label htmlFor="name">NAME*:</label>
          <input
            type="text"
            id="name"
            name="name"
            value={formData.name}
            onChange={handleChange}
            required
          />
        </div>

        <div className="form-group">
          <label> GENDER*:</label>
          <div className="gender-options">
            <label>
              <input
                type="radio"
                name="gender"
                value="Male"
                checked={formData.gender === 'Male'}
                onChange={handleChange}
                required
              />
              Male
            </label>
            <label>
              <input
                type="radio"
                name="gender"
                value="Female"
                checked={formData.gender === 'Female'}
                onChange={handleChange}
                required
              />
              Female
            </label>
            <label>
              <input
                type="radio"
                name="gender"
                value="Other"
                checked={formData.gender === 'Other'}
                onChange={handleChange}
                required
              />
              Other
            </label>
          </div>
        </div>

        <div className="form-group">
          <label htmlFor="dob">DATE OF BIRTH*:</label>
          <div className="dob">
            <input
              type="number"
              id="day"
              name="day"
              placeholder="Day"
              value={formData.day}
              onChange={handleChange}
              required
            />
            <input
              type="number"
              id="month"
              name="month"
              placeholder="Month"
              value={formData.month}
              onChange={handleChange}
              required
            />
            <input
              type="number"
              id="year"
              name="year"
              placeholder="Year"
              value={formData.year}
              onChange={handleChange}
              required
            />
          </div>
        </div>

        <div className="form-group">
          <label htmlFor="email">EMAIL ID*:</label>
          <input
            type="email"
            id="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="contactNumber">CONTACT NUMBER*:</label>
          <input
            type="text"
            id="contactNumber"
            name="contactNumber"
            value={formData.contactNumber}
            onChange={handleChange}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="expectedCTC">Expected CTC*:</label>
          <input
            type="text"
            id="expectedCTC"
            name="expectedCTC"
            value={formData.expectedCTC}
            onChange={handleChange}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="experienceYears">EXPERIENCE IN YEARS*:</label>
          <select
            id="experienceYears"
            name="experienceYears"
            value={formData.experienceYears}
            onChange={handleChange}
            required
          >
            <option value="">Select</option>
            <option value="0 to 1">0 to 1</option>
            <option value="1 to 3">1 to 3</option>
            <option value="3 to 5">3 to 5</option>
            <option value="5 to 10">5 to 10</option>
            <option value="10 or more">10 or more</option>
          </select>
        </div>

        <div className="form-group">
          <label htmlFor="resume">UPLOAD RESUME*:</label>
          <input
            type="file"
            id="resume"
            name="resume"
            accept=".pdf"
            onChange={handleFileChange}
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="captcha">RESOLVE THE CAPTCHA*:</label>
          <input
            type="text"
            id="captcha"
            name="captcha"
            value={formData.captcha}
            onChange={handleChange}
            required
          />
        </div>

          <button type="submit" className="submit-button1">
            Submit Your Application
          </button>
        </form>
      </div>
      <Footer />
    </div>
  );
};

export default Career;
