import React, { useEffect, useState } from 'react';
import axios from 'axios'
import DatePicker from "react-datepicker";
import TimePicker from 'react-datepicker';
import DateTimePicker from 'react-datetime-picker';

import "react-datepicker/dist/react-datepicker.css";
import { Link } from 'react-router-dom';


export default function IndAdminHome() {

  const countries = ['Singapore', 'Thailand', 'Malaysia', 'Indonesia', 'Vietnam', 'India'];

  const [activecomponent, setActiveComponent] = useState(1)
  const [isDataAvailable, setisDataAvailable] = useState(false)
  const [users, setusers] = useState([]);
  const [databyuserid, setdatabyuserid] = useState({})
  const [activeid, setactiveid] = useState(null)
  const [country, setCountry] = useState('')
  const [message, setMessage] = useState("")
  const [date, setDate] = useState("");
  const [subscriptionDate, setSubscriptiondate] = useState("")
  const [subscriptiontime, setsubscriptiontime] = useState('')
  const [cpreferralhistory, setCpreferralhistory] = useState({
    date: "",
    LeadName: '',
    solutionType: '',
    categoryPremises: ''
  })
  const [servicehistory, setServicehistory] = useState({
    subscriptiondate: "",
    subscriptiontime: "",
    servicetype: '',
    maintainenceEng: '',
    problemfixed: '',
  })

  function formatDateFromString(dateString) {
    const date = new Date(dateString);
    const day = String(date.getDate()).padStart(2, '0');
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const year = date.getFullYear();
    return `${day}/${month}/${year}`;
  }
  function formatTimeFromString(timeString) {
    const date = new Date(timeString);
    const hours = date.getHours();
    const minutes = date.getMinutes();
    const amOrPm = hours >= 12 ? 'PM' : 'AM';
    const formattedHours = (hours % 12 === 0 ? 12 : hours % 12).toString().padStart(2, '0');
    const formattedMinutes = minutes.toString().padStart(2, '0');
    const formattedTime = `${formattedHours}:${formattedMinutes} ${amOrPm}`;
    return formattedTime
  }

  let name, value
  const handleOnChangeRefHistory = (e) => {
    name = e.target.name
    value = e.target.value
    setCpreferralhistory({ ...cpreferralhistory, [name]: value })
  }

  const handleOnServiceHistory = (e) => {
    name = e.target.name
    value = e.target.value
    setServicehistory({ ...servicehistory, [name]: value })
  }



  const getlistofusers = async () => {
    try {
      setdatabyuserid({})
      setisDataAvailable(false)
      const res = await axios.get(`/api/individual/users/${country}`)
      if (res.data.success) {
        setusers(res.data.data)
      }
      else {
        window.alert('error in fetching')
      }
    }
    catch (err) {
      console.log(err)
    }
  }
  const getuserdetails = async (id) => {
    try {
      setdatabyuserid({})
      setisDataAvailable(false)
      setActiveComponent(1)
      const res = await axios.get(`/api/individual/userdetailbyid/${id}`)
      if (res.data.success) {
        console.log(res.data.data)
        setdatabyuserid(res.data.data)
        setisDataAvailable(true)
      }
      else {
        window.alert('failed to get user data by referral id')
        setisDataAvailable(true)
      }
    } catch (err) {
      console.log(err)
    }
  }
  const handleSubmitMessage = async () => {
    // try {
    //   const res = await axios.post(`/api/sendmessgetouser/${databyreferralid.referralID}`, { message })
    //   if (res.data.success) {
    //     window.alert('successfully send the message')
    //     setMessage('')
    //   }
    //   else {
    //     window.alert('failed to send message')
    //   }

    // } catch (err) {
    //   console.log(err)
    // }
  }
  const SubmitCpRefHistory = async () => {
    // try {
    //   const formattedDate = formatDateFromString(date);
    //   cpreferralhistory.date = formattedDate
    //   // console.log(cpreferralhistory)
    //   // console.log(databyreferralid._id)
    //   const res = await axios.post(`/api/updatecprefhistory/${databyreferralid._id}`, { cpreferralhistory })
    //   if (res.data.success) {
    //     window.alert('successfully updated the referral history')
    //   }
    //   else {
    //     window.alert('failed to update referral history')
    //   }

    // } catch (err) {
    //   console.log(err)
    // }
  }

  const SubmitServiceHistory = async () => {
    try {
      const formattedDate = formatDateFromString(subscriptionDate)
      servicehistory.subscriptiondate = formattedDate
      const formatedtime = formatTimeFromString(subscriptiontime)
      servicehistory.subscriptiontime = formatedtime
      const res = await axios.post(`/api/individual/updateservicehistory/${databyuserid._id}`, { servicehistory })
      if (res.data.success) {
        setServicehistory({})
        window.alert('successfully updated the service history')
      }
      else {
        window.alert('failed to update service history')
      }
    } catch (err) {
      console.log(err)
    }

  }
  return (
    <>
      <nav class=" p-4 " style={{ backgroundColor: '#233849' }}>
        <div class="flex justify-between items-center px-10">
          <div class="text-white">
            <a href="/userhome" class="flex items-center">
              <img src="https://flowbite.com/docs/images/logo.svg" class="h-8 mr-3" alt="Flowbite Logo" />
              <span class="self-center text-xl font-semibold whitespace-nowrap dark:text-white">Logo</span>
            </a>
          </div>
          <div class="text-white">
            <div class="items-center justify-between hidden w-full md:flex md:w-auto md:order-1" id="navbar-sticky">
              <h3 className='text-white text-xl' >Individual Dashboard</h3>
            </div>
          </div>
          <div style={{ display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
            <button onClick={() => setActiveComponent(5)} style={{ marginTop: "6px" }} type="button" class="text-white hover:text-white border border-gray-800 hover:bg-gray-900  focus:outline-none focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2 dark:border-gray-600 dark:text-white dark:hover:text-white dark:hover:bg-gray-600 dark:focus:ring-gray-800">Message</button>
            {/* <Logout/> */}
          </div>

        </div>
      </nav>
      <div style={{ display: 'flex', width: '100%', height: "88vh" }}>
        <div style={{ height: '88vh', width: '15%', backgroundColor: '#8a7c90', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
          <select
            id="country"
            className="mt-3 p-2 block w-10/12 rounded-md border  focus:outline-none bg-white"
            require
            onChange={(e) => {
              setCountry(e.target.value)
            }}
            name='country'
          >
            {/* <option value="">select</option>
            <option value="Singapore">Singapore</option>
            <option value="thailand">Thailand</option> */}
            {countries.map((country, index) => (
              <option key={index} value={country}>
                {country}
              </option>
            ))}
          </select>
          <button onClick={getlistofusers} class="relative inline-flex items-center justify-center p-0.5 mb-2 mr-2 mt-2 overflow-hidden text-sm font-medium text-gray-900 rounded-lg group bg-gradient-to-br from-purple-600 to-blue-500 group-hover:from-purple-600 group-hover:to-blue-500 hover:text-white dark:text-white focus:ring-4 focus:outline-none focus:ring-blue-300 dark:focus:ring-blue-800">
            <span class="relative px-5 py-2.5 transition-all ease-in duration-75 bg-white dark:bg-gray-900 rounded-md group-hover:bg-opacity-0">
              Filter
            </span>
          </button>
          {users.length > 0 ? users.map((user, index) => (
            <div style={{ display: 'flex', flexDirection: 'column', alignItems: "center", justifyContent: 'center', backgroundColor: `${activeid === user._id ? '#e44c4c' : "#8a7c90"}`, width: "100%", height: "30px", borderBottom: "1px solid", cursor: "pointer" }} onClick={() => {
              getuserdetails(user._id)
              setactiveid(user._id)
            }} >
              <button key={index}><div style={{ fontSize: "16px", marginTop: "6px", color: "white" }}>{user.name}</div></button>
            </div>
          )) : (

            <div style={{ width: '100%', height: "50vh", display: 'flex', alignItems: 'center', justifyContent: 'center' }}>

              <div style={{ width: '50%', color: 'white' }}>No Data found for this country</div>
            </div>
          )}
        </div>
        <div style={{ height: '88vh', width: "85%", display: 'flex', flexDirection: 'column' }}>
          <div className='font-medium' style={{ height: "40px", width: '100%', display: 'flex', justifyContent: 'space-between', alignItems: "center", padding: '5px 0px', color: "#352b2b", borderBottom: "1px solid" }}>
            <div style={{ backgroundColor: `${activecomponent === 1 ? "green" : 'white'}`, color: `${activecomponent === 1 ? "white" : 'black'}`, width: "25%", height: "40px", display: "flex", alignItems: 'center', justifyContent: 'center', cursor: 'pointer' }} onClick={() => setActiveComponent(1)} >List of Customers</div>
            <div style={{ backgroundColor: `${activecomponent === 2 ? "green" : 'white'}`, color: `${activecomponent === 2 ? "white" : 'black'}`, width: "25%", height: "40px", display: "flex", alignItems: 'center', justifyContent: 'center', cursor: 'pointer' }} onClick={() => setActiveComponent(2)}>Update Payments</div>
            <div style={{ backgroundColor: `${activecomponent === 3 ? "green" : 'white'}`, color: `${activecomponent === 3 ? "white" : 'black'}`, width: "25%", height: "40px", display: "flex", alignItems: 'center', justifyContent: 'center', cursor: 'pointer' }} onClick={() => setActiveComponent(3)}>Update Service History</div>
            <div style={{ backgroundColor: `${activecomponent === 4 ? "green" : 'white'}`, color: `${activecomponent === 4 ? "white" : 'black'}`, width: "25%", height: "40px", display: "flex", alignItems: 'center', justifyContent: 'center', cursor: 'pointer' }} onClick={() => setActiveComponent(4)}>Pending Service Request</div>
          </div>
          <div style={{ width: "100%", height: "83vh", display: "flex", flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
            {activecomponent === 1 && isDataAvailable === true ? (
              <div style={{ height: '80vh', width: "85%", display: 'flex', flexDirection: 'column' }}>
                <div style={{ width: "100%", height: "83vh", display: "flex", flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
                  <div className="grid grid-cols-2 gap-4 p-4" style={{ width: "95%" }}>
                    <div className="bg-gray-200 p-4 mr-3" style={{ display: "flex", alignItems: 'center', borderRadius: "5px" }}>
                      <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                        <h2 >Name</h2>
                        <h2 style={{ marginRight: '20px' }}>:</h2>
                      </div>
                      <h3 style={{ marginLeft: "10px" }}>{databyuserid?.firstName + " " + databyuserid?.middleName + " " + databyuserid?.lastName}</h3>
                    </div>

                    <div className="bg-gray-200 p-4 ml-3" style={{ display: "flex", alignItems: 'center', borderRadius: "5px" }}>
                      <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                        <h2 >Premise Type</h2>
                        <h2 style={{ marginRight: '20px' }}>:</h2>
                      </div>
                      <h3 style={{ marginLeft: '10px' }}>{databyuserid?.premisetype}</h3>
                    </div>
                    <div className="bg-gray-200 p-4 mr-3 " style={{ display: "flex", alignItems: 'center', borderRadius: "5px" }}>
                      <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                        <h2 >Email</h2>
                        <h2 style={{ marginRight: '20px' }}>:</h2>
                      </div>
                      <h3 style={{ marginLeft: '10px' }}>{databyuserid?.email}</h3>
                    </div>
                    <div className="bg-gray-200 p-4 ml-3" style={{ display: "flex", alignItems: 'center', borderRadius: "5px" }}>
                      <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                        <h2 >Address</h2>
                        <h2 style={{ marginRight: '20px' }}>:</h2>
                      </div>
                      <h3 style={{ marginLeft: '10px' }}>{databyuserid?.address}</h3>
                    </div>
                    <div className="bg-gray-200 p-4 mr-3" style={{ display: "flex", alignItems: 'center', borderRadius: "5px" }}>
                      <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                        <h2 >Contact</h2>
                        <h2 style={{ marginRight: '20px' }}>:</h2>
                      </div>
                      <h3 style={{ marginLeft: '10px' }}>{databyuserid?.contact}</h3>


                    </div>
                    <div className="bg-gray-200 p-4 ml-3" style={{ display: "flex", alignItems: 'center', borderRadius: "5px" }}>
                      <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                        <h2 >Country</h2>
                        <h2 style={{ marginRight: '20px' }}>:</h2>
                      </div>
                      <h3 style={{ marginLeft: '10px' }}>{databyuserid?.country}</h3>

                    </div>




                  </div>
                </div>
              </div>
            ) : activecomponent === 1 ? (
              <div>Not data Found</div>
            ) : null}
            {activecomponent === 2 && (
              <div>Update payment</div>
            )}
            {activecomponent === 3 && (
              <div style={{ height: '80vh', width: "85%", display: 'flex', flexDirection: 'column' }}>
                <div style={{ width: "100%", height: "83vh", display: "flex", flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
                  <div class="form-group row" style={{ margin: "7px 5px", width: "60%", display: 'flex', justifyContent: 'space-between' }} >
                    <label for="staticEmail" class="col-sm-3 col-form-label" style={{ fontSize: "20px", marginTop: '5px' }} >Service Type</label>
                    <div class="col-sm-5">
                      <select
                        className="mt-1 p-2 block w-full rounded-md border border-gray-300 focus:ring focus:ring-indigo-200 focus:outline-none focus:border-indigo-400"
                        required
                        style={{ width: '20rem' }}
                        name='servicetype'
                        value={servicehistory.servicetype}
                        onChange={handleOnServiceHistory}
                      >
                        <option value="">select</option>
                        <option value="Electrical">Electrical</option>
                        <option value="Carpenter">Carpenter</option>
                        <option value="Plumbing">Plumbing</option>
                        <option value="Computer">Computer</option>
                        <option value="Cleaning">Cleaning</option>
                      </select>
                    </div>
                  </div>
                  <div>
                  </div>

                  <div className="grid grid-cols-2 gap-4 p-4" style={{ width: '90%' }}>

                    <div className="bg-gray-200 p-4">Date</div>
                    <div style={{ width: "470px", display: 'flex', alignItems: 'center', }}> <DatePicker className='w-96 h-10 p-2.5 shadow appearance-none border rounded text-gray-700 leading-tight focus:outline-none focus:shadow-outline' selected={subscriptionDate} dateFormat="dd/MM/yyyy" onChange={(date) => setSubscriptiondate(date)} placeholderText='DD/MM/YYYY' /></div>


                    <div className="bg-gray-200 p-4">Time</div>
                    <div style={{ width: "470px", display: 'flex', alignItems: 'center', }}>
                      <TimePicker
                        selected={subscriptiontime}
                        onChange={(time) => setsubscriptiontime(time)}
                        showTimeSelect
                        showTimeSelectOnly
                        timeIntervals={15}
                        dateFormat="h:mm aa"
                        timeCaption="Time"
                        placeholderText='HH:mm'
                        className='w-96  h-10 p-2.5 shadow appearance-none border rounded text-gray-700 leading-tight focus:outline-none focus:shadow-outline'
                      />
                    </div>



                    <div className="bg-gray-200 p-4">Maintenance Engineer</div>
                    <input name='maintainenceEng' value={servicehistory.maintainenceEng} onChange={handleOnServiceHistory} class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Enter Here" />


                    <div className="bg-gray-200 p-4">Problem Fixed</div>
                    <input name='problemfixed' value={servicehistory.problemfixed} onChange={handleOnServiceHistory} class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Enter Here" />


                  </div>
                  <button onClick={SubmitServiceHistory} type="button" style={{ margin: '10px' }} class="text-white bg-gradient-to-r from-green-400 via-green-500 to-green-600 hover:bg-gradient-to-br focus:ring-4 focus:outline-none focus:ring-green-300 dark:focus:ring-green-800 shadow-lg shadow-green-500/50 dark:shadow-lg dark:shadow-green-800/80 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2">Submit</button>
                </div>
              </div>
            )}
            {activecomponent === 4 && (
              <div>pending service request</div>
            )}
            {activecomponent === 5 && (
              <div style={{ height: '80vh', width: "85%", display: 'flex', flexDirection: 'column' }}>
                <div style={{ width: "100%", height: "70vh", display: "flex", flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
                  <h1 style={{ fontSize: "25px", margin: '10px' }}>Message</h1>
                  <textarea value={message} onChange={(e) => setMessage(e.target.value)} style={{ width: "40rem", height: "140px", border: '1px solid', margin: "10px", padding: "5px", borderRadius: '5px' }} class="form-control" id="exampleFormControlTextarea1" rows="3" placeholder='Type Message'></textarea>
                  <button onClick={handleSubmitMessage} type="button" style={{ margin: '10px' }} class="text-white bg-gradient-to-r from-green-400 via-green-500 to-green-600 hover:bg-gradient-to-br focus:ring-4 focus:outline-none focus:ring-green-300 dark:focus:ring-green-800 shadow-lg shadow-green-500/50 dark:shadow-lg dark:shadow-green-800/80 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2">Send Message</button>
                </div>
              </div>
            )}
          </div>
        </div>

      </div>
    </>
  );
}
