import axios from 'axios'
import React from 'react'
import { useNavigate } from 'react-router-dom'

export default function Logout() {
    const navigate = useNavigate()
    const handleLogout = async ()=>{
        try{
            const res = await axios.get('/api/logout')
            if(res.data.success){
                localStorage.removeItem('token')
                window.alert('successfully logout')
                navigate('/')
            }
        }catch(err){
            console.log(err)
        }
    }
  return (
        <div style={{display:'flex',justifyContent:'center',alignItems:'center',marginTop:"6px"}}>
            <button style={{display:'flex',justifyContent:'center',alignItems:'center'}} type="button" class="text-white bg-gradient-to-r from-red-400 via-red-500 to-red-600 hover:bg-gradient-to-br focus:ring-4 focus:outline-none focus:ring-red-300 dark:focus:ring-red-800 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2"
        onClick={handleLogout}
        >Logout</button>
        </div>
        
  )
}
