import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import Sidebar from './IndComponents/Sidebar'
import Navbar from './IndComponents/Navbar'
import axios from 'axios';

export default function ServiceHistory() {
  const { user } = useSelector((state) => state.user);
  const [serviceHistories, setServicehistories] = useState([]);
  const [selectedFilter, setSelectedFilter] = useState('All');
  const [filteredData,setfilteredData] = useState([])

  const fetchServiceHistories = async () => {
    try {
      setSelectedFilter('All')
      const res = await axios.get('/api/individual/servicehistories');
      if (res.data.success && res.data.data) {
        setServicehistories(res.data.data);
        setfilteredData(res.data.data)
      }
    } catch (err) {
      console.error(err);
    }
  };
  const Handlefilter = ()=>{
    if(selectedFilter==='All'){
       setfilteredData(serviceHistories)
    }
    else{
       const data = serviceHistories.filter(item => item.servicetype === selectedFilter);
       setfilteredData(data)
    }
  }
  useEffect(() => {
    fetchServiceHistories();
  }, []);

  return (
    <>
      <Navbar />
      <div style={{ display: 'flex', alignItems: 'center' }}>
        <Sidebar />
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            width: '84%',
            height: '88vh',
            overflowY: 'scroll',
          }}
        >
          <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', height: "20vh",width:"60%" }}>
            <h1 style={{ fontSize: '30px', marginTop: "10px" }}>Service Type</h1>
            <div style={{ display: 'flex', alignItems: 'center', marginBottom: '20px',justifyContent:"space-around",width:"70%",height:'10vh' }}>
              <select
                className=" p-2 block w-full rounded-md"
                style={{ width: '20rem', marginLeft: '20px', border: '1px solid' }}
                required
                onChange={(e) => setSelectedFilter(e.target.value)}
                value={selectedFilter}
              >
                <option value='All'>All</option>
                <option value="Electrical">Electrical</option>
                <option value="Carpenter">Carpenter</option>
                <option value="Plumbing">Plumbing</option>
                <option value="Computer">Computer</option>
                <option value="Cleaning">Cleaning</option>
              </select>
              <button type="button" onClick={Handlefilter}  class="mt-1 text-gray-900 bg-white border border-gray-300 focus:outline-none hover:bg-gray-100 focus:ring-2 focus:ring-gray-200 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-gray-800 dark:text-white dark:border-gray-600 dark:hover:bg-gray-700 dark:hover:border-gray-600 dark:focus:ring-gray-700">Filter</button>
            </div>
            
          </div>
          <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', width: '80%' }}>
            {filteredData.length==0 ?(
              <div>No data found</div>
            ):(
              <>
              {filteredData.map((history, index) => (
                <div key={history._id} className="grid grid-cols-2" style={{ margin: '10px', width: '70%' }}>
                  <div style={{ border: '1px solid ', height: '8vh', padding: '5px', fontSize: '18px',fontWeight:"500" }}>Date</div>
                  <div style={{ border: '1px solid ', height: '8vh', padding: '5px', fontSize: '18px' }}>{history.subscriptiondate}</div>
                  <div style={{ border: "1px solid", height: '8vh', padding: "5px", fontSize: "18px",fontWeight:"500" }}>Time</div>
                  <div style={{ border: "1px solid ", height: '8vh', padding: "5px", fontSize: "18px" }}>{history.subscriptiontime}</div>
                  <div style={{ border: "1px solid ", height: '8vh', padding: "5px", fontSize: "18px",fontWeight:"500" }}>Maintenance Engineer</div>
                  <div style={{ border: "1px solid ", height: '8vh', padding: "5px", fontSize: "18px" }}>{history.maintainenceEng}</div>
                  <div style={{ border: "1px solid ", height: '8vh', padding: "5px", fontSize: "18px",fontWeight:"500" }}>Problem Fixed</div>
                  <div style={{ border: "1px solid", height: '8vh', padding: "5px", fontSize: "18px" }}>{history.problemfixed}</div>
                  <div style={{ border: "1px solid ", height: '8vh', padding: "5px", fontSize: "18px",fontWeight:"500" }}>Service ID</div>
                  <div style={{ border: "1px solid", height: '8vh', padding: "5px", fontSize: "18px" }}>xxxxxx(six digits)</div>
                  <div style={{ border: "1px solid ", height: '8vh', padding: "5px", fontSize: "18px",fontWeight:"500" }}>Give Us Rating</div>
                  <div style={{ border: "1px solid", height: '8vh', padding: "5px", fontSize: "18px" }}>rating</div>
                </div>
              ))}
              </>
            )}  
            
          </div>

        </div>
      </div>
    </>
  );
}

