import React, { useRef, useState, useEffect } from 'react'
import { useSelector } from 'react-redux'
import Logout from '../components/Logout'
import Notifications from '../pages/userpages.js/Notifications';
import axios from 'axios';
import logo from '../new.jpg'

export default function Navbar() {
  const { user } = useSelector(state => state.user)
  const [seennotification, setSeenNotification] = useState([]);
  const [notification, setNotification] = useState([]);

  const [isContainerOpen, setContainerOpen] = useState(false);
  const containerRef = useRef(null);

  const toggleContainer = () => {
    setContainerOpen(!isContainerOpen);
  };
  const closeContainer = async (e) => {
    try {

      if (containerRef.current && !containerRef.current.contains(e.target)) {
        setContainerOpen(false);
        console.log(notification.length)
        if (notification.length > 0) {
            setSeenNotification([...seennotification, ...notification]);
            setNotification([]);
            console.log(notification)
          const res = await axios.post('/api/updatenotifications');
          if (res.data.success) {
 
          } else {
            window.alert('error');
          }
        }
      }
    } catch (err) {
      console.log(err);
    }
  };

  useEffect(() => {
    if (user) {
      setSeenNotification(user.seenNotifications || []);
      setNotification(user.notifications || []);
    }
    document.addEventListener('mousedown', closeContainer);

    return () => {
      document.removeEventListener('mousedown', closeContainer);
    };
  }, [user,notification,seennotification]);
  return (
    <>
      <nav class=" p-4 " style={{ backgroundColor: '#233849' }}>
        <div class="flex justify-between items-center px-10">
          <div class="text-white">
            <a href="/userhome" class="flex items-center">
              <img src={logo} class="h-8 mr-3" alt="Flowbite Logo" style={{height:"5vh",borderRadius:"10px"}} />
            </a>
          </div>


          <div class="text-white">
            <div class="items-center justify-between hidden w-full md:flex md:w-auto md:order-1" id="navbar-sticky">
              <h3 className='text-white text-3xl' >Welcome {user?.firstName} into Your Dashboard</h3>
            </div>
          </div>


          <div class="text-white">
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', width: '10rem' }}>
              <div class="relative h-10 w-10 0" onClick={toggleContainer} style={{ cursor: 'pointer' }}>
                <div style={{ width: '22px', height: '22px', display: `${notification.length>0?'flex':"none"}`, alignItems: "center", justifyContent: 'center' }} class="absolute left-0 top-0  bg-red-500 rounded-full">
                  {
                    notification.length > 0 && (<span class="text-xs text-white p-1">{notification.length}</span>)
                  }
                </div>
                <div class="p-2">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 24 24"
                    fill="currentColor"
                    class=" text-white" style={{ height: '30px', width: '30px' }}>
                    <path
                      fill-rule="evenodd"
                      d="M5.25 9a6.75 6.75 0 0113.5 0v.75c0 2.123.8 4.057 2.118 5.52a.75.75 0 01-.297 1.206c-1.544.57-3.16.99-4.831 1.243a3.75 3.75 0 11-7.48 0 24.585 24.585 0 01-4.831-1.244.75.75 0 01-.298-1.205A8.217 8.217 0 005.25 9.75V9zm4.502 8.9a2.25 2.25 0 104.496 0 25.057 25.057 0 01-4.496 0z"
                      clip-rule="evenodd" />
                  </svg>
                </div>
              </div>
              <Logout />
            </div>
          </div>
        </div>
      </nav>
      {isContainerOpen && (
        <div ref={containerRef}>
          <Notifications notifications={notification} seenNotifications={seennotification} />
        </div>
      )}
    </>
  )
}
