import React from 'react';
import { Link } from 'react-router-dom'; // Import Link from react-router-dom
import './RefundPolicy.css';
import Navbar from './Navbar';
import Footer from './Footer';

const  RefundPolicy= () => {
  return (
    <div className="containervv">
      <Navbar />

    <div className="cancellation-refund-policy">
      <h2 className="policy-title1">Return and Refund Policy</h2>
      <p className="last-updated">Last updated: October 06, 2023</p>
      <p className="thank-you">Thank you for shopping at FixTech Care.</p>
      <p className="intro-text">If, for any reason, You are not completely satisfied with a purchase, We invite You to review our policy on refunds and returns. The following terms are applicable for any products that You purchased with Us.</p>
      <h2 className="section-titleno">Interpretation and Definitions</h2>
      <h3 className="sub-section-titleno">Interpretation</h3>
      <p className="sub-section-text">The words of which the initial letter is capitalized have meanings defined under the following conditions. The following definitions shall have the same meaning regardless of whether they appear in singular or in plural.</p>
      <h3 className="sub-section-titleno">Definitions</h3>
      <p className="sub-section-text">For the purposes of this Return and Refund Policy:</p>
      <ul className="definitions-list">
        <li><strong className="company">Company</strong> (referred to as either "the Company", "We", "Us" or "Our" in this Agreement) refers to FixTech Facility Services, Mumbai.</li>
        <li><strong className="goods">Goods</strong> refer to the items offered for sale on the Service.</li>
        <li><strong className="orders">Orders</strong> mean a request by You to purchase Goods from Us.</li>
        <li><strong className="services">Service</strong> refers to the Website.</li>
        <li><strong className="website">Website</strong> refers to FixTech Care, accessible from <Link to="/YourFile" className="external-link">www.fixtechcare.com</Link></li>
        <li><strong className="you">You</strong> means the individual accessing or using the Service, or the company, or other legal entity on behalf of which such individual is accessing or using the Service, as applicable.</li>
      </ul>
      <h2 className="section-titleno">Your Order Cancellation Rights</h2>
      <p className="sub-section-text">You are entitled to cancel Your Order within 14 days without giving any reason for doing so.</p>
      <p className="sub-section-text">The deadline for cancelling an Order is 14 days from the date on which You received the Goods or on which a third party you have appointed, who is not the carrier, takes possession of the product delivered.</p>
      <p className="sub-section-text">In order to exercise Your right of cancellation, You must inform Us of your decision by means of a clear statement. You can inform us of your decision by:</p>
      <ul className="cancellation-methods">
        <li>By email: <a href="mailto:feedback@fixtechcare.com" className="emailno">feedback@fixtechcare.com</a></li>
      </ul>
      <p className="sub-section-text">We will reimburse You no later than 14 days from the day on which We receive the returned Goods. We will use the same means of payment as You used for the Order, and You will not incur any fees for such reimbursement.</p>
      <h2 className="section-titleno">Conditions for Returns</h2>
      <p className="sub-section-text">In order for the Goods to be eligible for a return, please make sure that:</p>
      <ul className="conditions-list">
        <li>The Goods were purchased in the last 14 days</li>
        <li>The Goods are in the original packaging</li>
      </ul>
      <p className="sub-section-text">The following Goods cannot be returned:</p>
      <ul className="unreturnable-goods-list">
        <li>The supply of Goods made to Your specifications or clearly personalized.</li>
        <li>The supply of Goods which according to their nature are not suitable to be returned, deteriorate rapidly or where the date of expiry is over.</li>
        <li>The supply of Goods which are not suitable for return due to health protection or hygiene reasons and were unsealed after delivery.</li>
        <li>The supply of Goods which are, after delivery, according to their nature, inseparably mixed with other items.</li>
      </ul>
      <p className="sub-section-text">We reserve the right to refuse returns of any merchandise that does not meet the above return conditions in our sole discretion.</p>
      <p className="sub-section-text">Only regular priced Goods may be refunded. Unfortunately, Goods on sale cannot be refunded. This exclusion may not apply to You if it is not permitted by applicable law.</p>
      <h2 className="section-titleno">Returning Goods</h2>
      <p className="sub-section-text">You are responsible for the cost and risk of returning the Goods to Us. You should send the Goods at the following address:</p>
      <p className="return-addressno">Mumbai Maharashtra India</p>
      <p className="sub-section-text">We cannot be held responsible for Goods damaged or lost in return shipment. Therefore, We recommend an insured and trackable mail service. We are unable to issue a refund without actual receipt of the Goods or proof of received return delivery.</p>
      <h2 className="section-titleno">Gifts</h2>
      <p className="sub-section-text">If the Goods were marked as a gift when purchased and then shipped directly to you, You'll receive a gift credit for the value of your return. Once the returned product is received, a gift certificate will be mailed to You.</p>
      <p className="sub-section-text">If the Goods weren't marked as a gift when purchased, or the gift giver had the Order shipped to themselves to give it to You later, We will send the refund to the gift giver.</p>
      <h3 className="contact-titleno">Contact Us</h3>
      <p className="contact-textno">If you have any questions about our Returns and Refunds Policy, please contact us:</p>
      <ul className="contact-methods">
        <li>By email: <a href="mailto:feedback@fixtechcare.com" className="emailno">feedback@fixtechcare.com</a></li>
      </ul>
      </div>
      <Footer />
    </div>
  );
}

export default RefundPolicy;
