import React from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import './TestimonialSlider.css'; 

const testimonials = [
  {
    id: 1,
    text: "The team provided top-notch electrical and electronics maintenance, ensuring our systems are running smoothly. Fast and efficient!.",
    author: "John Doe",
  },
  {
    id: 2,
    text: "I was impressed by their plumbing maintenance expertise. Reliable and trustworthy service that I can count on.",
    author: "Jane Smith",
  },
  {
    id: 3,
    text: "Reliable computer maintenance for both hardware and software! They kept our systems in perfect condition. A tech-savvy team!",
    author: "Alice Johnson",
  },
  {
    id: 4,
    text: "Highly skilled carpenters who transformed our space! Their carpentry services exceeded our expectations. Great craftsmanship!",
    author: "Bob Brown",
  },
  {
    id: 5,
    text: "The premises review service was invaluable. They provided a detailed assessment, helping us identify areas for improvement",
    author: "Eva White",
  },
];

const TestimonialSlider = () => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  return (
    <Slider {...settings}>
      {testimonials.map((testimonial) => (
        <div key={testimonial.id} className="testimonial-slide">
          <p className="testimonial-text">{testimonial.text}</p>
          <p className="testimonial-author">
            - {testimonial.author}{" "}
            <br></br>
            <span className="star-rating">{Array(5).fill("").join("")}</span>
          </p>
        </div>
      ))}
    </Slider>
  );
};

function App() {
  return (
    <div className="App">
     
      <section className="other-section">
      
        <TestimonialSlider />

       
        <div className="styled-box">
          <p className="testimonial-text"></p>
        </div>
      </section>
    
    </div>
  );
}

export default App;
