import React from 'react'
import Navbar from '../../components/Navbar'
import Sidebar from '../../components/Sidebar'

export default function SupportAccount() {
  return (
    <>
      <Navbar />
      <div style={{ display: 'flex' }}>
        <Sidebar />
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: "center", width: "84%", height: "88vh"}}>
          <div style={{ height: '80vh', width: "80%",  padding: "20px" }}>
            <h3>If you have any query raise ticket : </h3>
            <h3>Email us at : <a href='partner@fixtechcare.com' style={{ color: "blue" }}>partner@fixtechcare.com</a> </h3>
            <h3>Relationship Manager : xxxxxxxx </h3>
            <div style={{ height: "65vh", width: "90%", marginTop: "10px", marginLeft: 'auto', display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
              <div class="form-group row" style={{ margin: "7px 5px", width: "50%", display: 'flex', justifyContent: 'space-between' }} >
                <label for="staticEmail" class="col-sm-3 col-form-label" style={{ fontSize: "20px", marginTop: '5px' }} >Subject<span style={{color:'red'}}>*</span></label>
                <div class="col-sm-5">
                  <input type="text" class="form-control-plaintext" id="staticEmail" placeholder="" style={{ width: "20rem", height: "40px", padding: '3px',border:'1px solid' }} />
                </div>
              </div>
              <div class="form-group row" style={{ margin: "7px 5px", width: "50%", display: 'flex', justifyContent: 'space-between' }} >
                <label for="staticEmail" class="col-sm-3 col-form-label" style={{ fontSize: "20px", marginTop: '5px' }} >Type</label>
                <div class="col-sm-5">
                  <select
                    id="city"
                    className="mt-1 p-2 block w-full rounded-md border border-gray-300 focus:ring focus:ring-indigo-200 focus:outline-none focus:border-indigo-400"
                    required
                    style={{ width: '20rem' }}
                    name='city'
                  >
                    <option value="">select</option>
                    <option value="New Mexico">Account</option>
                    <option value="Missouri">Payout</option>
                    <option value="Texas">Others</option>
                    <option value="Texas">Complaint</option>
                    <option value="Texas">Question</option>
                  </select>
                </div>
              </div>
              <div class="form-group row" style={{ marginTop: '50px', width: "50%", display: 'flex', justifyContent: 'space-between' }} >
                <label for="staticEmail" class="col-sm-3 col-form-label" style={{ fontSize: "20px", marginTop: '5px' }} >Description</label>
                <div class="col-sm-5">
                  <textarea class="form-control" id="exampleFormControlTextarea1" rows="3" style={{ height: "100px", width: "20rem",border:"1px solid" }}></textarea>
                </div>
              </div>
              <div class="form-group row" style={{ marginTop: '50px', width: "50%", display: 'flex', flexDirection: 'column', justifyContent: 'space-between' }} >

                <label class="block mb-2 text-sm font-medium text-gray-900 " for="file_input">Upload file</label>
                <input class="block w-full text-sm text-gray-900 border border-gray-300 rounded-lg cursor-pointer bg-gray-50 dark:text-gray-400 focus:outline-none dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400" aria-describedby="file_input_help" id="file_input" type="file" />
                <p class="mt-1 text-sm " id="file_input_help">SVG, PNG, JPG or GIF (MAX. 800x400px).</p>
              </div>
              <div style={{marginTop:"20px", width: '50%', display: 'flex', justifyContent: 'center' }}>
                <button style={{marginRight:'10px'}} class="bg-transparent hover:bg-green-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded">
                  Submit
                </button>
                <button style={{marginLeft:'10px'}} class="bg-transparent hover:bg-red-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded">
                  Cancel
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>

    </>
  )
}
