import React, { useState } from 'react'
import Navbar from '../../components/Navbar'
import Sidebar from '../../components/Sidebar'
import { useSelector } from 'react-redux'
import axios from 'axios'

export default function AccountDetails() {
  const { user } = useSelector(state => state.user)
  // const { referralID, businessCategory, email, firstName, lastName, businessName, contact, idActivateOn,address } = user
  const [edit,setedit] = useState(false)
  const [newuserdata,setNewUserData] = useState({
    businessName:"",
    firstName:"",
    lastName:"",
    contact:"",
    address:""
  })
  const handleEdit = ()=>{
    setedit(true)
  }
  const handleSave = async ()=>{
    try{
      if(newuserdata.businessName==='') newuserdata.businessName = user?.businessName
      if(newuserdata.firstName==='') newuserdata.firstName= user?.firstName
      if(newuserdata.lastName==='') newuserdata.lastName=user?.lastName
      if(newuserdata.contact==='') newuserdata.contact=user?.contact
      if(newuserdata.address==='') newuserdata.address=user?.address
      const res = await axios.post('/api/updateuserdetails',newuserdata)
      if(res.data.success){
        setedit(false)
        window.location.reload()
      }
      else{
        window.alert('failed to edit data')
        setedit(false)
      }
    }catch(err){
      console.log(err)
    }

  }
  let name,value
  const handleonChange = (e)=>{
    name = e.target.name
    value = e.target.value
    setNewUserData({...newuserdata,[name]:value})
  }
  return (
    <>
      <Navbar />
      <div style={{ display: 'flex' }}>
        <Sidebar />

        { edit ? (
          <div style={{ display: 'flex', alignItems: 'center', justifyContent: "center", width: "84%", height: "88vh" }}>
          <div style={{ width: "60%", height: "80vh", padding: "5px 10px", borderRadius: "20px", border: "2px solid" }}>
            <h1 style={{ fontSize: '30px' }}>Account Details</h1>
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', padding: '1px 10px' }}>
              <h2 style={{ fontSize: '20px' }}>User Profile</h2>
              <button type="button" class="text-gray-900 bg-white border border-gray-300 focus:outline-none hover:bg-gray-100 focus:ring-4 focus:ring-gray-200 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-gray-800 dark:text-white dark:border-gray-600 dark:hover:bg-gray-700 dark:hover:border-gray-600 dark:focus:ring-gray-700"
                style={{ marginRight: "30px" }}
                onClick={handleSave}
              >save</button>
            </div>
            <div style={{ display: "flex", alignItems: "center", marginLeft: "40%" }}>
              <img src='https://img.freepik.com/free-vector/businessman-character-avatar-isolated_24877-60111.jpg?w=2000' style={{ width: '100px', height: "100px", borderRadius: '50%' }} />
              <button type="button" class="text-gray-900 bg-white border border-gray-300 focus:outline-none hover:bg-gray-100 focus:ring-4 focus:ring-gray-200 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-gray-800 dark:text-white dark:border-gray-600 dark:hover:bg-gray-700 dark:hover:border-gray-600 dark:focus:ring-gray-700"
                style={{ marginLeft: "20px", marginTop: "25px", padding: '5px', width: "10rem" }}
              ><h6 style={{ fontSize: '12px' }}>Upload</h6><h6 style={{ fontSize: '12px' }}>Profile Picture</h6></button>
            </div>
            <div style={{ width: '100%', height: '52vh', display: 'flex', justifyContent: 'center' }}>
              <div style={{ width: '80%', height: "50vh", marginTop: "20px", padding: '5px' }}>
                <div class="grid grid-cols-2 gap-4">

                  <div>My Referral ID</div>
                  <div>{user?.referralID}</div>

                  <div>Business Category</div>
                  <div>{user?.businessCategory}</div>


                  <div>Business Name</div>
                  <div><input type="text" name='businessName' value={newuserdata.businessName} class="form-control-plaintext"  placeholder={user?.businessName} onChange={handleonChange} style={{ width: "20rem", height: "25px", padding: '2px',border:'1px solid' }} /></div>


                  <div>ID Activated on</div>
                  <div>{user?.idActivateOn}</div>


                  <div>Email ID</div>
                  <div>{user?.email}</div>


                  <div>First Name</div>
                  <div><input type="text" name='firstName' value={newuserdata.firstName}  class="form-control-plaintext" placeholder={user?.firstName} onChange={handleonChange} style={{ width: "20rem", height: "25px", padding: '2px',border:'1px solid' }} /></div>


                  <div>Last Name</div>
                  <div><input type="text" name='lastName' value={newuserdata.lastName} class="form-control-plaintext"  placeholder={user?.lastName} onChange={handleonChange} style={{ width: "20rem", height: "25px", padding: '2px',border:'1px solid' }} /></div>


                  <div>Contact</div>
                  <div><input type="text" name='contact' value={newuserdata.contact}  class="form-control-plaintext"  placeholder={user?.contact} onChange={handleonChange} style={{ width: "20rem", height: "25px", padding: '2px',border:'1px solid' }} /></div>


                  <div>Address</div>
                  <div><input type="text" name='address' value={newuserdata.address}  class="form-control-plaintext" placeholder={user?.address} onChange={handleonChange} style={{ width: "20rem", height: "25px", padding: '2px',border:'1px solid' }} /></div>
                </div>


              </div>
            </div>

          </div>
        </div>
        ):(
                  <div style={{ display: 'flex', alignItems: 'center', justifyContent: "center", width: "84%", height: "88vh" }}>
                  <div style={{ width: "60%", height: "80vh", padding: "5px 10px", borderRadius: "20px", border: "2px solid" }}>
                    <h1 style={{ fontSize: '30px' }}>Account Details</h1>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', padding: '1px 10px' }}>
                      <h2 style={{ fontSize: '20px' }}>User Profile</h2>
                      <button type="button" class="text-gray-900 bg-white border border-gray-300 focus:outline-none hover:bg-gray-100 focus:ring-4 focus:ring-gray-200 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-gray-800 dark:text-white dark:border-gray-600 dark:hover:bg-gray-700 dark:hover:border-gray-600 dark:focus:ring-gray-700"
                        style={{ marginRight: "30px" }}
                        onClick={handleEdit}
                      >Edit</button>
                    </div>
                    <div style={{ display: "flex", alignItems: "center", marginLeft: "40%" }}>
                      <img src='https://img.freepik.com/free-vector/businessman-character-avatar-isolated_24877-60111.jpg?w=2000' style={{ width: '100px', height: "100px", borderRadius: '50%' }} />
                      <button type="button" class="text-gray-900 bg-white border border-gray-300 focus:outline-none hover:bg-gray-100 focus:ring-4 focus:ring-gray-200 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-gray-800 dark:text-white dark:border-gray-600 dark:hover:bg-gray-700 dark:hover:border-gray-600 dark:focus:ring-gray-700"
                        style={{ marginLeft: "20px", marginTop: "25px", padding: '5px', width: "10rem" }}
                      ><h6 style={{ fontSize: '12px' }}>Upload</h6><h6 style={{ fontSize: '12px' }}>Profile Picture</h6></button>
                    </div>
                    <div style={{ width: '100%', height: '52vh', display: 'flex', justifyContent: 'center' }}>
                      <div style={{ width: '80%', height: "50vh", marginTop: "20px", padding: '5px' }}>
                        <div class="grid grid-cols-2 gap-4">
        
                          <div>My Referral ID</div>
                          <div>{user?.referralID}</div>
        
        
                          <div>Business Category</div>
                          <div>{user?.businessCategory}</div>
        
        
                          <div>Business Name</div>
                          <div>{user?.businessName}</div>
        
        
                          <div>ID Activated on</div>
                          <div>{user?.idActivateOn}</div>
        
        
                          <div>Email ID</div>
                          <div>{user?.email}</div>
        
        
                          <div>First Name</div>
                          <div>{user?.firstName}</div>
        
        
                          <div>Last Name</div>
                          <div>{user?.lastName}</div>
        
        
                          <div>Contact</div>
                          <div>{user?.contact}</div>
        
        
                          <div>Address</div>
                          <div>{user?.address}</div>
                        </div>
        
        
                      </div>
                    </div>
        
                  </div>
                </div>
        ) }


      </div>

    </>
  )
}
