import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import axios from 'axios'
// import { useDispatch } from 'react-redux'
// import { showLoading,hideLoading } from '../redux/features/alertSlice'

export default function IndAdmRegister() {
    const navigate = useNavigate()
    // const dispatch = useDispatch()

    const [user, setUser] = useState({
        name: "",
        email: "",
        password: ""
    })
    let name, value
    const handleOnChange = (event) => {
        name = event.target.name
        value = event.target.value
        setUser({ ...user, [name]: value })
    }

    const handleOnClick = async (event) => {
        event.preventDefault()
        try {
            // dispatch(showLoading())
            // console.log(user)
            const res = await axios.post('/api/individual/adminregister',user)
            // dispatch(hideLoading())
            if (res.status === 200) {
                window.alert("successfully registered")
                navigate("/individual/adminlogin")
            }
            else{
                // dispatch(hideLoading())
                window.alert('email already Exist')
            }
        } catch (err) {
            console.log(err)
        }
    }
    return (
        <div style={{ display: 'flex', alignItems: 'center', height: '70vh' }} >
            <div class="container" style={{
                display: 'flex', flexDirection: 'column', justifyContent: 'center', width: '450px', height: '40vh',
                borderRadius: '10px', boxShadow: 'rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px'
            }} >
                <div class="form-group row" style={{ margin: "7px 5px" }} >
                    <label for="staticEmail" class="col-sm-3 col-form-label" >Name</label>
                    <div class="col-sm-5">
                        <input type="text" class="form-control-plaintext" name='name' value={user.name} onChange={handleOnChange} id="staticEmail" placeholder="Enter Your Name" />
                    </div>
                </div>
                <div class="form-group row" style={{ margin: "7px 5px" }} >
                    <label for="staticEmail" class="col-sm-3 col-form-label" >Email</label>
                    <div class="col-sm-5">
                        <input type="text" class="form-control-plaintext" name='email' value={user.email} onChange={handleOnChange} id="staticEmail" placeholder="Enter Your email id" />
                    </div>
                </div>
                <div class="form-group row" style={{ margin: "7px 5px" }}>
                    <label for="staticEmail" class="col-sm-3 col-form-label" >Password</label>
                    <div class="col-sm-5">
                        <input type="text" class="form-control-plaintext" name='password' value={user.password} onChange={handleOnChange} id="staticEmail" placeholder="Password" />
                    </div>
                </div>
                <div className='buttonclass' style={{ display: 'flex', justifyContent: 'center', marginTop: '10px' }}>
                    <button type="button" class="btn btn-success" onClick={handleOnClick} >Submit</button>
                </div>
            </div>
        </div>
    )
}