import React from 'react';
import './Features.css';
import new2b from './TIMEIMAGE/new2b.jpg';

const Features = () => {
  return (
    <div className="features-container">
      <div className="left-side">
        <h4 className="feature-heading">Features We Provide</h4>
        <ul>
          <li className="feature-item">The only Subscription Based Facility Maintenance Service Provider.</li>
          <li className="feature-item">Improved clarity of solutions and process will not be tedious since we will be giving service at a
convenient price.</li>
          <li className="feature-item">Impressive and permanent solutions with customer satisfaction.</li>
          <li className="feature-item">100+ services under single package.</li>
          <li className="feature-item">Client can Track service records by just login into dashboard.</li>
          <li className="feature-item">24x7 Help Desk.</li>
         
        </ul>
      </div>
      <div className="right-side">
        <img src={new2b} alt="Left-side Img" />
      </div>
    </div>
  );
};

export default Features;
















