import React, { useState } from 'react'
import Sidebar from './IndComponents/Sidebar'
import Navbar from './IndComponents/Navbar'
import { Link } from 'react-router-dom';

export default function FAQ() {

  const [openQuestion, setOpenQuestion] = useState(null);

  const questions = [
    {
      id: 1,
      question: 'Q1. How does i Signup?',
      answer:
        'answer',
    },
    {
      id: 2,
      question: 'Q2. What methods of payment do you accept?',
      answer:
        'answer.',
    },
    {
      id: 3,
      question: 'Q3. How do i cancel my subscription?',
      answer:
        'answer',
    },
    {
      id: 4,
      question: 'Q4. What do you mean by services allotted?',
      answer:
        'answer',
    },
    {
      id: 5,
      question: 'Q5. How do i retrieve my username or password?',
      answer:
        'answer',
    },
    {
      id: 6,
      question: 'Q6. What if i dont pay my subscription fees?',
      answer:
        'This is the third item\'s accordion body. It is hidden by default, until the collapse plugin adds the appropriate classes that we use to style each element. These classes control the overall appearance, as well as the showing and hiding via CSS transitions. You can modify any of this with custom CSS or overriding our default variables. It\'s also worth noting that just about any HTML can go within the `.accordion-body`, though the transition does limit overflow.',
    },
    {
      id: 7,
      question: 'Q7. How do i update my account information?',
      answer:
        'answer',
    },
    {
      id: 8,
      question: 'Q8. What if i didnt satisfy with your service engineer?',
      answer:
        'answer',
    },
    {
      id: 9,
      question: 'Q9. How will be i beneficial by subscription with your service?',
      answer:
        'answer',
    },
    {
      id: 10,
      question: 'Q10. Will you add more types of services in your package?',
      answer:
        'answer',
    },
  ];

  const toggleQuestion = (id) => {
    if (openQuestion === id) {
      setOpenQuestion(null);
    } else {
      setOpenQuestion(id);
    }
  };

  return (
    <>
      <Navbar />
      <div style={{ display: 'flex' }}>
        <Sidebar />
        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: "center", width: "84%", height: "88vh" }}>
          <div style={{ width: "70%", }}>
            <h2 style={{ fontSize: "30px" }}>Frequently Asked Questions</h2>
          </div>
          <div style={{ width: "70%" }}>
            <h2 style={{ fontSize: "15px" }}>Questions You May Have - Everything You Need To Know</h2>
          </div>
          <div className="accordion" style={{ width: "70%", height: '70vh', marginTop: "20px" }}>
            {questions.map((q) => (
              <div key={q.id} className="accordion-item" style={{ margin: '5px' }}>
                <h2 className="accordion-header">
                  <div style={{ width: '100%', height: "40px", border: '1px solid', borderRadius: "4px", display: 'flex', alignItems: 'center', justifyContent: "space-between" }}
                    className={`accordion-button ${openQuestion === q.id ? '' : 'collapsed'
                      }`}
                    type="button"
                    onClick={() => toggleQuestion(q.id)}
                  >
                    <h3 style={{ marginLeft: '10px' }}>{q.question}</h3>
                    {openQuestion === q.id ? (
                      <svg style={{ marginRight: "20px" }} xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" class="w-6 h-6">
                        <path stroke-linecap="round" stroke-linejoin="round" d="M19.5 12h-15" />
                      </svg>
                    ) : (
                      <svg style={{ marginRight: "20px" }} xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6">
                        <path strokeLinecap="round" strokeLinejoin="round" d="M12 4.5v15m7.5-7.5h-15" />
                      </svg>
                    )

                    }


                  </div>
                </h2>
                {openQuestion === q.id && (
                  <div className="accordion-collapse" style={{ fontSize: '14px', padding: '5px' }}>
                    <div className="accordion-body">{q.answer}</div>
                  </div>
                )}

              </div>
            ))}
            <div style={{ width: '100%', height: "50px", padding: "5px", display: "flex", alignItems: "center", justifyContent: "space-between" }}>
              <h1 style={{ marginLeft: "20px" }}>Still not Found what you were looking For ?</h1>
              <Link style={{ marginRight: "20px", color: "blue" }} >Raise Your Ticket now to get Help </Link>
            </div>
          </div>
        </div>

      </div>


    </>
  )
}