import React, { useState } from 'react';

import './DashboardLogin.css';
import { useNavigate } from 'react-router-dom';

const Login = () => {
  const [showUserLogin, setShowUserLogin] = useState(true);
  const [showChannelPartnerLogin, setShowChannelPartnerLogin] = useState(false);
  const navigate = useNavigate()

  const toggleUserLogin = () => {
    setShowUserLogin(true);
    setShowChannelPartnerLogin(false);
  };

  const toggleChannelPartnerLogin = () => {
    setShowUserLogin(false);
    setShowChannelPartnerLogin(true);
    navigate('/userlogin')
    
  };

  return (
    <div className="login-container">
      <div className="popup-heading">
        {showUserLogin && <h2 className="user-heading">User Login</h2>}
        {showChannelPartnerLogin && <h2 className="partner-heading">Channel Partner Login</h2>}
      </div>

      {showUserLogin && (
        <div className="user-login">
          <div className="input-containOFF">
            <input type="text" className='channelOFF'placeholder="Username" />
          </div>
          <div className="input-containOFF">
            <input type="Password"className='channelOFF' placeholder="Password" />
          </div>
          <div className="remember-me">
            <input type="checkbox" id="remember" />
            <label htmlFor="remember">Remember me</label>
          </div>
          <button className="toggle-button" onClick={toggleChannelPartnerLogin}>
            Channel Partner Login
          </button>
          <button className="login-button">Login</button>

       
        
        </div>
      )}

      {showChannelPartnerLogin && (
        <div className="channel-partner-login">
          <div className="input-containOFF">
            <input type="email" className='channelOFF' placeholder="Email" />
          </div>
          <div className="remember-me">
            <input type="checkbox" id="remember" />
            <label htmlFor="remember">Remember me</label>
          </div>
          <button className="toggle-button" onClick={toggleUserLogin}>
            User Login
          </button>
          <button className="login-button">Login</button>

        
        
        </div>
      )}
    </div>
  );
};

export default Login;