import React, { useEffect, useState } from 'react';
import Navbar from '../../components/Navbar';
import Sidebar from '../../components/Sidebar';
import { useSelector } from 'react-redux';
import axios from 'axios';
import { startOfMonth, startOfDay, isAfter, isBefore, addMonths, subDays, subMonths, subYears, subWeeks } from 'date-fns';

export default function CommissionsHistory() {
  const { user } = useSelector((state) => state.user);
  const [comHistories, setComHistories] = useState([]);
  const [selectedFilter, setSelectedFilter] = useState('All');

  const fetchComHistories = async () => {
    try {
      const res = await axios.get(`/api/commissionhistorybyid`);
      if (res.data.success && res.data.data) {
        setComHistories(res.data.data);
      }
    } catch (err) {
      console.error(err);
    }
  };

  useEffect(() => {
    fetchComHistories();
  }, []);

  let currentDate = new Date();
  
  let last30Days = new Date();
  last30Days.setDate(currentDate.getDate() - 30);

  let last7Days = new Date();
  last7Days.setDate(currentDate.getDate() - 7);

  let lastYear = new Date();
  lastYear.setFullYear(currentDate.getFullYear() - 1);

  const filteredComHistories = comHistories.filter((history) => {

    if (selectedFilter === '1 week') {
      let itemDateParts = history.subscriptiondate.split('/');
      let itemDate = new Date(itemDateParts[2], itemDateParts[1] - 1, itemDateParts[0]);
      return itemDate >= last7Days && itemDate <= currentDate;
    } else if (selectedFilter === '1 month') {
      let itemDateParts = history.subscriptiondate.split('/');
      let itemDate = new Date(itemDateParts[2], itemDateParts[1] - 1, itemDateParts[0]);
      return itemDate >= last30Days && itemDate <= currentDate;
    }
    else if (selectedFilter === '1 year') {
      let itemDateParts = history.subscriptiondate.split('/');
      let itemDate = new Date(itemDateParts[2], itemDateParts[1] - 1, itemDateParts[0]);
      return itemDate >= lastYear && itemDate <= currentDate;
    }

    return true;
  });



  return (
    <>
      <Navbar />
      <div style={{ display: 'flex', alignItems: 'center' }}>
        <Sidebar />
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            // justifyContent: 'center',
            width: '84%',
            height: '88vh',
            overflowY: 'scroll',
          }}
        >
          <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', height: "20vh" }}>
            <h1 style={{ fontSize: '30px' }}>List Of All Commissions History</h1>
            <div style={{ display: 'flex', alignItems: 'center', marginBottom: '20px' }}>
              <label htmlFor="Business" className="block text-s font-medium">
                Select
              </label>
              <select
                id="businessCategory"
                className="mt-1 p-2 block w-full rounded-md"
                style={{ width: '20rem', marginLeft: '20px', border: '1px solid' }}
                required
                onChange={(e) => setSelectedFilter(e.target.value)}
                value={selectedFilter}
              >
                <option value='All'>All</option>
                <option value="1 week">1 week</option>
                <option value="1 month">1 month</option>
                <option value="1 year">1 year</option>
              </select>
            </div>
          </div>
          <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', width: '80%' }}>
            {filteredComHistories.map((history, index) => (
              <div key={history._id} className="grid grid-cols-2" style={{ margin: '10px', width: '70%' }}>
                <div style={{ border: '1px solid ', height: '8vh', padding: '5px', fontSize: '18px' }}>Date of Subscription</div>
                <div style={{ border: '1px solid ', height: '8vh', padding: '5px', fontSize: '18px' }}>{history.subscriptiondate}</div>
                <div style={{ border: "1px solid", height: '8vh', padding: "5px", fontSize: "18px" }}>Name of the lead</div>
                <div style={{ border: "1px solid ", height: '8vh', padding: "5px", fontSize: "18px" }}>{history.LeadName}</div>
                <div style={{ border: "1px solid ", height: '8vh', padding: "5px", fontSize: "18px" }}>Solution Type</div>
                <div style={{ border: "1px solid ", height: '8vh', padding: "5px", fontSize: "18px" }}>{history.solutionType}</div>
                <div style={{ border: "1px solid ", height: '8vh', padding: "5px", fontSize: "18px" }}>Commission Amount</div>
                <div style={{ border: "1px solid", height: '8vh', padding: "5px", fontSize: "18px" }}>{history.commissionAmount}</div>
                <div style={{ border: "1px solid ", height: '8vh', padding: "5px", fontSize: "18px" }}>Payment Mode</div>
                <div style={{ border: "1px solid", height: '8vh', padding: "5px", fontSize: "18px" }}>{history.paymentMode}</div>

              </div>
            ))}
          </div>

        </div>
      </div>
    </>
  );
}


