import axios from 'axios';
import React, { useEffect, useState } from 'react';
import Popup from 'reactjs-popup';
import 'reactjs-popup/dist/index.css';
import { useNavigate } from 'react-router-dom';
import logo from '../new.jpg'
import Navbar from '../Component/Navbar';

export default function RegisterForm() {
    const navigate = useNavigate()
    const [user, setUser] = useState({
        country: "",
        businessCategory: "",
        email: "",
        firstName: "",
        middleName: "",
        lastName: "",
        businessName: "",
        contact: "",
        city: "",
        state: "",
        zipCode: "",
    })
    const randomString = Math.random().toString(36).slice(8)
    const [captcha, setCaptcha] = useState(randomString)
    const [text, setText] = useState("")
    const [valid, setValid] = useState(false)
    const [show, setShow] = useState(false)
    const [referralID, setreferralID] = useState('')
    const [allID, setAllId] = useState([])

    const countries = ['India', 'Australia', 'New Zealand', 'Thailand', 'Singapore', 'UAE', 'Hong Kong',"United Kingdom", "Germany", "France", "Italy", "Netherlands", "Ireland", "Switzerland", "Poland", "Belgium", "Portugal", "Spain", "Slovakia", "Greece", "Austria", "Hungary", " Czech Republic", "Serbia", "Denmark", "Sweden", "Norway", "Lithuania", "Romania","Canada", "USA"]

    const refreshString = () => {
        setCaptcha(Math.random().toString(36).slice(8))
    }

    let name, value
    const handleChange = (e) => {
        name = e.target.name
        value = e.target.value
        setUser({ ...user, [name]: value })
    }

    const generateUniqueRandomID = (allID) => {
        const usedIDs = new Set(allID);
        let randomID;
        do {
            randomID = Math.random().toString(36).slice(2, 8);
        } while (usedIDs.has(randomID));

        return randomID;
    }

    const handleSubmit = async (e) => {
        e.preventDefault()
        try {
            if (text === captcha) {
                const newreferralId = generateUniqueRandomID(allID)
                setreferralID(newreferralId)
                setValid(false)
                setShow(true)
                const requestData = {
                    ...user,
                    referralID: newreferralId,
                };
                const res = await axios.post('/api/userregister', requestData)
                if (res.status === 200) {
                    window.alert('sucessfully submited')
                    // navigate('/userlogin')
                } else {
                    window.alert('errr')
                }
            } else {
                setValid(true)
                window.alert('wrong captcha')
            }
        } catch (err) {
            console.log(err)
        }
    }

    const allreferralids = async () => {
        try {
            const res = await axios.get('/api/getallreferralid')
            if (res.status === 200) {
                console.log(res.data.data)
                setAllId(res.data.data)
            } else {
                console.log('failed to get all refrral ids')
            }
        } catch (err) {
            console.log(err)
        }
    }
    useEffect(() => {
        allreferralids()
    }, [])

    return (
        <>
        <Navbar/>
        <div className="min-h-screen flex items-center justify-center">
            <div className="max-w-2xl w-full space-y-8 p-7 bg-gray-900 shadow-lg rounded-lg text-white">
                <h2  className="text-2xl font-extrabold text-center">Get Your Channel Partner ID</h2>
                <div  className="mt-6 space-y-3 ">
                    <div className="grid grid-cols-2 gap-4 mb-5">
                        <div style={{display:'flex',flexDirection:"column"}}>
                            <label htmlFor="state" className="block text-sm font-medium text-gray-500">
                                Country
                            </label>
                            <select
                                id="country"
                                className="mt-1 p-2 block w-full rounded-md border  border-gray-300 focus:ring focus:ring-indigo-200 focus:outline-none focus:border-indigo-400 bg-gray-900"
                                require
                                onChange={handleChange}
                                value={user.country}
                                name='country'
                            >
                                <option value='' >select</option>
                                {countries.map((country)=>(
                                    <option value={country} >{country}</option>
                                ))}
                            </select>
                        </div>
                        <div>
                            <label htmlFor="Business" className="block text-sm font-medium text-gray-500">
                                Business Category
                            </label>
                            <select
                                id="businessCategory"
                                className="mt-3 p-2 block w-full rounded-md border border-gray-300 focus:ring focus:ring-indigo-200 focus:outline-none focus:border-indigo-400 bg-gray-900"
                                required
                                onChange={handleChange}
                                value={user.businessCategory}
                                name='businessCategory'
                            >
                                <option value="">select</option>
                                <option value="New Mexico">Real State Promoter</option>
                                <option value="Missouri">Estate agents</option>
                                <option value="Texas">Property Consultant</option>
                                <option value="Texas">Electrical Stores</option>
                                <option value="Texas">Hardware Stores</option>
                                <option value="Texas">Architecture</option>
                                <option value="Texas">Inferior Designer</option>
                                <option value="Texas">Freelancer</option>
                            </select>
                        </div>
                    </div>
                    <div class="relative z-0 w-full mb-6 group">
                        <input type="email" name="email" id="floating_email" style={{backgroundColor:"transparent",width:"100%",borderTop:"none",borderLeft:'none',borderRight:'none',borderRadius:'0px',borderBottom:"1px solid rgb(75 85 99 "}} class="block py-2.5 px-0 w-full text-sm text-gray-900 border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer" onChange={handleChange} value={user.email} required />
                        <label for="floating_email" class="peer-focus:font-medium absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-2 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6">Email ID</label>
                    </div>
                    <div class="relative z-0 w-full mb-6 group">
                        <input type="text" name="firstName" id="floating_firstname" style={{backgroundColor:"transparent",width:"100%",borderTop:"none",borderLeft:'none',borderRight:'none',borderRadius:'0px',borderBottom:"1px solid rgb(75 85 99 "}} class="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer" onChange={handleChange} value={user.firstName} required />
                        <label for="floating_email" class="peer-focus:font-medium absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-2 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6">First Name</label>
                    </div>
                    <div class="relative z-0 w-full mb-6 group">
                        <input type="text" name="middleName" id="floating_middlename"style={{backgroundColor:"transparent",width:"100%",borderTop:"none",borderLeft:'none',borderRight:'none',borderRadius:'0px',borderBottom:"1px solid rgb(75 85 99 "}}class="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer" onChange={handleChange} value={user.middleName} />
                        <label for="floating_email" class="peer-focus:font-medium absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-2 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6">Middle Name</label>
                    </div>
                    <div class="relative z-0 w-full mb-6 group">
                        <input type="text" name="lastName" id="floating_lastname" style={{backgroundColor:"transparent",width:"100%",borderTop:"none",borderLeft:'none',borderRight:'none',borderRadius:'0px',borderBottom:"1px solid rgb(75 85 99 "}} class="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer" required onChange={handleChange} value={user.lastName} />
                        <label for="floating_email" class="peer-focus:font-medium absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-2 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6">Last Name</label>
                    </div>
                    <div class="relative z-0 w-full mb-6 group">
                        <input type="text" name="businessName" id="floating_businessname" style={{backgroundColor:"transparent",width:"100%",borderTop:"none",borderLeft:'none',borderRight:'none',borderRadius:'0px',borderBottom:"1px solid rgb(75 85 99 "}} class="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer" required onChange={handleChange} value={user.businessName} />
                        <label for="floating_email" class="peer-focus:font-medium absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-2 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6">Business Name</label>
                    </div>
                    <div class="relative z-0 w-full mb-6 group">
                        <input type="tel" name="contact" id="floating_phone" style={{backgroundColor:"transparent",width:"100%",borderTop:"none",borderLeft:'none',borderRight:'none',borderRadius:'0px',borderBottom:"1px solid rgb(75 85 99 "}} class="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer" required onChange={handleChange} value={user.contact} />
                        <label for="floating_phone" class="peer-focus:font-medium absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-2 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6">Contact</label>
                    </div>

                    <div className="grid grid-cols-3 gap-4" >
                        <div>
                            <label htmlFor="city" className="block text-sm font-medium text-gray-500">
                                City
                            </label>
                            <select
                                id="city"
                                className="mt-1 p-2 block w-full rounded-md border border-gray-300 focus:ring focus:ring-indigo-200 focus:outline-none focus:border-indigo-400 bg-gray-900"
                                required
                                onChange={handleChange}
                                value={user.city}
                                name='city'
                            >
                                <option value="">select</option>
                                <option value="New Mexico">New Mexico</option>
                                <option value="Missouri">Missouri</option>
                                <option value="Texas">Texas</option>
                            </select>
                        </div>
                        <div>
                            <label htmlFor="state" className="block text-sm font-medium text-gray-500">
                                State
                            </label>
                            <select
                                id="state"
                                name='state'
                                className="mt-1 p-2 block w-full rounded-md border border-gray-300 focus:ring focus:ring-indigo-200 focus:outline-none focus:border-indigo-400 bg-gray-900"
                                required
                                onChange={handleChange}
                                value={user.state}
                            >
                                <option value="">select</option>
                                <option value="New Mexico">New Mexico</option>
                                <option value="Missouri">Missouri</option>
                                <option value="Texas">Texas</option>
                            </select>
                        </div>
                        <div>
                            <label htmlFor="zip" className="block text-sm font-medium text-gray-500">
                                Zip Code
                            </label>
                            <input
                                type="text"
                                name='zipCode'
                                id="zip"
                                className="mt-1 p-1 block w-full rounded-md border border-gray-300 focus:ring focus:ring-indigo-200 focus:outline-none focus:border-indigo-400 bg-gray-900"
                                placeholder=""
                                onChange={handleChange}
                                value={user.zipCode}
                                style={{backgroundColor:"transparent",height:"2.5rem"}}
                            />
                        </div>
                    </div>

                    <div className="flex  items-center justify-between">
                        <div className="flex items-center justify-between my-4 ml-7">
                            <div className="text-xl mx-2">{captcha}</div>
                            <button
                                onClick={() => refreshString()}
                                className="bg-gray-900 text-white font-bold py-2 px-4 rounded-full flex items-center"
                            >
                                <svg
                                    className="w-8 h-8 text-red-500"
                                    fill="none"
                                    stroke="currentColor"
                                    viewBox="0 0 24 24"
                                    xmlns="http://www.w3.org/2000/svg"
                                >
                                    <path
                                        strokeLinecap="round"
                                        strokeLinejoin="round"
                                        strokeWidth="2"
                                        d="M4.05 11a8 8 0 1 1 .5 4m-.5 5v-5h5"
                                    />
                                </svg>
                            </button>
                        </div>
                        <div class="relative z-0 w-1/2 mr-7 mb-1 mt-3 group">
                            <input type="text" name="captcha" id="captcha" style={{backgroundColor:"transparent",width:"100%",borderTop:"none",borderLeft:'none',borderRight:'none',borderRadius:'0px',borderBottom:"1px solid rgb(75 85 99 "}} class="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer" required onChange={(e) => { setText(e.target.value) }} value={text} />
                            <label for="floating_phone" class="peer-focus:font-medium absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-2 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6">Enter Captcha</label>
                        </div>
                    </div>

                    <div>
                        <button
                            type="submit"
                            className="group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                            onClick={handleSubmit}
                        >
                            Submit
                        </button>
                    </div>
                </div>
                <Popup trigger=
                    {show && <button style={{ backgroundColor: "orange", padding: '3px', borderRadius: '5px',height:"8vh" }}>Click here to get Referal ID</button>}
                    modal nested 
                    onClose={() => {
                        navigate('/userlogin')
                    }}
                    >
                    {
                        close => (
                            <div className='modal' style={{height:"40vh",width:"50#",display:'flex',alignItems:"center",justifyContent:"center"}}>
                                <h2 style={{fontWeight:"500",fontSize:"25px"}}>{`Save Your Channel Partner ID :${referralID} `}</h2>
                            </div>
                        )
                    }
                </Popup>
            </div>
        </div>
        </>
    );
}
