import React, { useEffect, useState } from 'react';
import Logout from '../components/Logout';
import axios from 'axios'
import DatePicker from "react-datepicker";
import logo from '../new.jpg'

import "react-datepicker/dist/react-datepicker.css";
import { Link } from 'react-router-dom';


export default function AdminHome() {

  const countries = ['India', 'Australia', 'New Zealand', 'Thailand', 'Singapore', 'UAE', 'Hong Kong',"United Kingdom", "Germany", "France", "Italy", "Netherlands", "Ireland", "Switzerland", "Poland", "Belgium", "Portugal", "Spain", "Slovakia", "Greece", "Austria", "Hungary", " Czech Republic", "Serbia", "Denmark", "Sweden", "Norway", "Lithuania", "Romania","Canada", "USA"]

  const [activecomponent, setActiveComponent] = useState(1)
  const [isDataAvailable, setisDataAvailable] = useState(false)
  const [referralIDs, setReferralIDs] = useState([]);
  const [databyreferralid, setdatabyreferralid] = useState({})
  const [activeid, setactiveid] = useState(null)
  const [country, setCountry] = useState('')
  const [message, setMessage] = useState("")
  const [date, setDate] = useState("");
  const [subscriptionDate, setSubscriptiondate] = useState("")
  const [cpreferralhistory, setCpreferralhistory] = useState({
    date: "",
    LeadName: '',
    solutionType: '',
    categoryPremises: ''
  })
  const [cpCommissionhistory, setCpCommissionhistory] = useState({
    subscriptiondate: "",
    LeadName: '',
    solutionType: '',
    commissionAmount: '',
    paymentMode: ""
  })
  const [paymentDetails, setPaymentDetails] = useState([])
  const [isPaymentDataAvailable, setIsPayDataAvailable] = useState(false)

  function formatDateFromString(dateString) {
    const date = new Date(dateString);
    const day = String(date.getDate()).padStart(2, '0');
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const year = date.getFullYear();
    return `${day}/${month}/${year}`;
  }

  let name, value
  const handleOnChangeRefHistory = (e) => {
    name = e.target.name
    value = e.target.value
    setCpreferralhistory({ ...cpreferralhistory, [name]: value })
  }

  const handleOnChangeComHistory = (e) => {
    name = e.target.name
    value = e.target.value
    setCpCommissionhistory({ ...cpCommissionhistory, [name]: value })
  }



  const getlistofreferralid = async () => {
    try {
      setdatabyreferralid({})
      setisDataAvailable(false)
      const res = await axios.get(`/api/channelpartersbycountry/${country}`)
      if (res.data.success) {
        setReferralIDs(res.data.data)
      }
      else {
        window.alert('error in fetching')
      }
    }
    catch (err) {
      console.log(err)
    }
  }
  const getuserdetails = async (referralID) => {
    try {
      setdatabyreferralid({})
      setisDataAvailable(false)
      setIsPayDataAvailable(false)
      setActiveComponent(1)
      const res = await axios.get(`/api/channelpartnerbyreferralid/${referralID}`)
      if (res.data.success) {
        // console.log(res.data.data)
        setdatabyreferralid(res.data.data)
        setisDataAvailable(true)
      }
      else {
        window.alert('failed to get user data by referral id')
        setisDataAvailable(true)
      }
    } catch (err) {
      console.log(err)
    }
  }
  const handleSubmitMessage = async () => {
    try {
      const res = await axios.post(`/api/sendmessgetouser/${databyreferralid.referralID}`, { message })
      if (res.data.success) {
        window.alert('successfully send the message')
        setMessage('')
      }
      else {
        window.alert('failed to send message')
      }

    } catch (err) {
      console.log(err)
    }
  }
  const SubmitCpRefHistory = async () => {
    try {
      const formattedDate = formatDateFromString(date);
      cpreferralhistory.date = formattedDate
      // console.log(cpreferralhistory)
      // console.log(databyreferralid._id)
      const res = await axios.post(`/api/updatecprefhistory/${databyreferralid._id}`, { cpreferralhistory })
      if (res.data.success) {
        window.alert('successfully updated the referral history')
      }
      else {
        window.alert('failed to update referral history')
      }

    } catch (err) {
      console.log(err)
    }
  }

  const SubmitCpCommissionHistory = async () => {
    try {
      const formattedDate = formatDateFromString(subscriptionDate)
      cpCommissionhistory.subscriptiondate = formattedDate
      // console.log(cpCommissionhistory)
      // console.log(databyreferralid._id)
      const res = await axios.post(`/api/updatecpcommissionhistory/${databyreferralid._id}`, { cpCommissionhistory })
      if (res.data.success) {
        window.alert('successfully updated the commission history')
      }
      else {
        window.alert('failed to update commission history')
      }
    } catch (err) {
      console.log(err)
    }
  }

  const getuserpaymentdetails = async () => {
    try {
      const res = await axios.get(`/api/getuserpaymentdetails/${databyreferralid._id}`)
      if (res.data.success) {
        setPaymentDetails(res.data.data)
        console.log(res.data.data)
        setIsPayDataAvailable(true)
      } else {
        setIsPayDataAvailable(false)
      }
    } catch (err) {
      console.log(err)
    }
  }
  return (
    <>
      <nav class=" p-4 " style={{ backgroundColor: '#233849' }}>
        <div class="flex justify-between items-center px-10">
          <div class="text-white" style={{ width: "17rem", margin: '10px' }}>
                <a href="/" class="flex items-center">
                    <img src={logo} class="h-8 mr-3" alt="Flowbite Logo" style={{ height: "6vh", borderRadius: "10px" }} />
                </a>
            </div>
          <div class="text-white">
            <div class="items-center justify-between hidden w-full md:flex md:w-auto md:order-1" id="navbar-sticky">
              <h3 className='text-white text-xl' >Partner Dashboard</h3>
            </div>
          </div>
          <div style={{ display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
            <button onClick={() => setActiveComponent(5)} style={{ marginTop: "6px" }} type="button" class="text-white hover:text-white border border-gray-800 hover:bg-gray-900  focus:outline-none focus:ring-gray-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2 dark:border-gray-600 dark:text-white dark:hover:text-white dark:hover:bg-gray-600 dark:focus:ring-gray-800">Message</button>
            <Logout />
          </div>

        </div>
      </nav>
      <div style={{ display: 'flex', width: '100%', height: "88vh" }}>
        <div style={{ height: '88vh', width: '15%', backgroundColor: '#8a7c90', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
          <select
            id="country"
            className="mt-3 p-2 block w-10/12 rounded-md border  focus:outline-none bg-white"
            require
            onChange={(e) => {
              setCountry(e.target.value)
            }}
            name='country'
          >
            {countries.map((country, index) => (
              <option key={index} value={country}>
                {country}
              </option>
            ))}
          </select>
          <button onClick={getlistofreferralid} class="relative inline-flex items-center justify-center p-0.5 mb-2 mr-2 mt-2 overflow-hidden text-sm font-medium text-gray-900 rounded-lg group bg-gradient-to-br from-purple-600 to-blue-500 group-hover:from-purple-600 group-hover:to-blue-500 hover:text-white dark:text-white focus:ring-4 focus:outline-none focus:ring-blue-300 dark:focus:ring-blue-800">
            <span class="relative px-5 py-2.5 transition-all ease-in duration-75 bg-white dark:bg-gray-900 rounded-md group-hover:bg-opacity-0">
              Filter
            </span>
          </button>
          {referralIDs.length > 0 ? referralIDs.map((referralID, index) => (
            <div style={{ display: 'flex', flexDirection: 'column', alignItems: "center", justifyContent: 'center', backgroundColor: `${activeid === referralID ? '#e44c4c' : "#8a7c90"}`, width: "100%", height: "30px", borderBottom: "1px solid", cursor: "pointer" }} onClick={() => {
              getuserdetails(referralID)
              setactiveid(referralID)
            }} >
              <h3 key={index}><div style={{ fontSize: "16px", marginTop: "6px", color: "white" }}>{referralID}</div></h3>
            </div>
          )) : (

            <div style={{ width: '100%', height: "50vh", display: 'flex', alignItems: 'center', justifyContent: 'center' }}>

              <div style={{ width: '50%', color: 'white' }}>No Data found for this country</div>
            </div>
          )}
        </div>
        <div style={{ height: '88vh', width: "85%", display: 'flex', flexDirection: 'column' }}>
          <div className='font-medium' style={{ height: "40px", width: '100%', display: 'flex', justifyContent: 'space-between', alignItems: "center", padding: '5px 0px', color: "#352b2b", borderBottom: "1px solid" }}>
            <div style={{ backgroundColor: `${activecomponent === 1 ? "green" : 'white'}`, color: `${activecomponent === 1 ? "white" : 'black'}`, width: "25%", height: "40px", display: "flex", alignItems: 'center', justifyContent: 'center' }} onClick={() => setActiveComponent(1)} >List of Channel partner</div>
            <div style={{ backgroundColor: `${activecomponent === 2 ? "green" : 'white'}`, color: `${activecomponent === 2 ? "white" : 'black'}`, width: "25%", height: "40px", display: "flex", alignItems: 'center', justifyContent: 'center' }} onClick={() => setActiveComponent(2)}>update cp dashboard[referral history]</div>
            <div style={{ backgroundColor: `${activecomponent === 3 ? "green" : 'white'}`, color: `${activecomponent === 3 ? "white" : 'black'}`, width: "25%", height: "40px", display: "flex", alignItems: 'center', justifyContent: 'center' }} onClick={() => setActiveComponent(3)}>update cp dashboard[commission history]</div>
            <div style={{ backgroundColor: `${activecomponent === 4 ? "green" : 'white'}`, color: `${activecomponent === 4 ? "white" : 'black'}`, width: "25%", height: "40px", display: "flex", alignItems: 'center', justifyContent: 'center' }} onClick={() =>{ setActiveComponent(4)
            getuserpaymentdetails()}}>current payment mode of C.P.</div>
          </div>
          <div style={{ width: "100%", height: "83vh", display: "flex", flexDirection: 'column', alignItems: 'center', justifyContent: 'center',overflowY:"scroll" }}>
            {activecomponent === 1 && isDataAvailable === true ? (
              <div style={{ height: '80vh', width: "85%", display: 'flex', flexDirection: 'column' }}>
                <div style={{ width: "100%", height: "83vh", display: "flex", flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
                  <div className="grid grid-cols-2 gap-4 p-4" style={{ width: "95%" }}>
                    <div className="bg-gray-200 p-4 mr-3" style={{ display: "flex", alignItems: 'center', borderRadius: "5px" }}>
                      <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                        <h3 >Name</h3>
                        <h2 style={{ marginRight: '20px' }}>:</h2>
                      </div>
                      <h3 style={{ marginLeft: "10px" }}>{databyreferralid?.firstName + " " + databyreferralid?.middleName + " " + databyreferralid?.lastName}</h3>
                    </div>

                    <div className="bg-gray-200 p-4 ml-3" style={{ display: "flex", alignItems: 'center', borderRadius: "5px" }}>
                      <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                        <h3 >Business Name</h3>
                        <h2 style={{ marginRight: '20px' }}>:</h2>
                      </div>
                      <h3 style={{ marginLeft: '10px' }}>{databyreferralid?.businessName}</h3>
                    </div>
                    <div className="bg-gray-200 p-4 mr-3 " style={{ display: "flex", alignItems: 'center', borderRadius: "5px" }}>
                      <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                        <h3 >Email</h3>
                        <h2 style={{ marginRight: '20px' }}>:</h2>
                      </div>
                      <h3 style={{ marginLeft: '10px' }}>{databyreferralid?.email}</h3>
                    </div>
                    <div className="bg-gray-200 p-4 ml-3" style={{ display: "flex", alignItems: 'center', borderRadius: "5px" }}>

                      <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                        <h3 >Business Category</h3>
                        <h2 style={{ marginRight: '20px' }}>:</h2>
                      </div>
                      <h3 style={{ marginLeft: "10px" }}>{databyreferralid?.businessCategory}</h3>
                    </div>
                    <div className="bg-gray-200 p-4 mr-3" style={{ display: "flex", alignItems: 'center', borderRadius: "5px" }}>

                      <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                        <h3 >Address</h3>
                        <h2 style={{ marginRight: '20px' }}>:</h2>
                      </div>
                      <h3 style={{ marginLeft: '10px' }}>{databyreferralid?.address}</h3>
                    </div>
                    <div className="bg-gray-200 p-4 ml-3" style={{ display: "flex", alignItems: 'center', borderRadius: "5px" }}>
                      <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                        <h3 >Country</h3>
                        <h2 style={{ marginRight: '20px' }}>:</h2>
                      </div>
                      <h3 style={{ marginLeft: '10px' }}>{databyreferralid?.country}</h3>

                    </div>
                    <div className="bg-gray-200 p-4 mr-3" style={{ display: "flex", alignItems: 'center', borderRadius: "5px" }}>
                      <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                        <h3 >Contact</h3>
                        <h2 style={{ marginRight: '20px' }}>:</h2>
                      </div>
                      <h3 style={{ marginLeft: '10px' }}>{databyreferralid?.contact}</h3>


                    </div>
                    <div className="bg-gray-200 p-4 ml-3" style={{ display: "flex", alignItems: 'center', borderRadius: "5px" }}>
                      <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                        <h3 >State</h3>
                        <h2 style={{ marginRight: '20px' }}>:</h2>
                      </div>
                      <h3 style={{ marginLeft: '10px' }}>{databyreferralid?.state}</h3>
                    </div>
                    <div className="bg-gray-200 p-4 mr-3" style={{ display: "flex", alignItems: 'center', borderRadius: "5px" }}>
                      <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                        <h3 >ID Activated On</h3>
                        <h2 style={{ marginRight: '20px' }}>:</h2>
                      </div>
                      <h3 style={{ marginLeft: '10px' }}>{databyreferralid?.idActivateOn}</h3>

                    </div>
                    <div className="bg-gray-200 p-4 ml-3" style={{ display: "flex", alignItems: 'center', borderRadius: "5px" }}>
                      <div style={{ display: 'flex', alignItems: 'center', width: "50%" }}>
                        <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                          <h3 >City</h3>
                          <h2 style={{ marginRight: '20px' }}>:</h2>
                        </div>
                        <h3 style={{ marginLeft: "10px" }}>{databyreferralid?.city}</h3>
                      </div>
                      <div style={{ height: "30px", width: "1px", backgroundColor: "black", marginRight: '20px' }}></div>
                      <div style={{ display: 'flex', alignItems: 'center', width: "50%" }}>
                        <div style={{ width: '40%', display: "flex", alignItems: 'center', justifyContent: 'space-between' }}>
                          <h3 >ZipCode</h3>
                          <h2 style={{ marginRight: '20px' }}>:</h2>
                        </div>
                        <h3 style={{ marginLeft: '10px' }}>{databyreferralid?.zipCode}</h3>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ) : activecomponent === 1 ? (
              <div>Not data Found</div>
            ) : null}
            {activecomponent === 2 && (
              <div style={{ height: '80vh', width: "85%", display: 'flex', flexDirection: 'column' }}>
                <div style={{ width: "100%", height: "83vh", display: "flex", flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
                  <div className="grid grid-cols-2 gap-3 p-4" style={{ width: '90%' }}>

                    <div className="bg-gray-200 p-4">Date</div>
                    <div style={{ width: "470px", display: 'flex', alignItems: 'center', }}> <DatePicker className='w-96 h-10 p-2.5 shadow appearance-none border rounded  text-gray-700 leading-tight focus:outline-none focus:shadow-outline' selected={date} dateFormat="dd/MM/yyyy" onChange={(date) => setDate(date)} placeholderText='DD/MM/YYYY' /></div>


                    <div className="bg-gray-200 p-4">Name of the Lead</div>
                    <input name='LeadName'   value={cpreferralhistory.LeadName} onChange={handleOnChangeRefHistory} class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Enter Here" />


                    <div className="bg-gray-200 p-4">Solution Type</div>
                    <input name='solutionType' value={cpreferralhistory.solutionType} onChange={handleOnChangeRefHistory} class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Enter Here" />


                    <div className="bg-gray-200 p-4">Category of Premises</div>
                    <input name='categoryPremises' value={cpreferralhistory.categoryPremises} onChange={handleOnChangeRefHistory} class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Enter Here" />
                  </div>
                  <button onClick={SubmitCpRefHistory} type="button" style={{ margin: '10px' }} class="text-white bg-gradient-to-r from-green-400 via-green-500 to-green-600 hover:bg-gradient-to-br focus:ring-4 focus:outline-none focus:ring-green-300 dark:focus:ring-green-800 shadow-lg shadow-green-500/50 dark:shadow-lg dark:shadow-green-800/80 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2">Submit</button>
                </div>
              </div>
            )}
            {activecomponent === 3 && (
              <div style={{ height: '80vh', width: "85%", display: 'flex', flexDirection: 'column' }}>
                <div style={{ width: "100%", height: "83vh", display: "flex", flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
                  <div className="grid grid-cols-2 gap-4 p-4" style={{ width: '90%' }}>

                    <div className="bg-gray-200 p-4">Date of Subscription</div>
                    <div style={{ width: "470px", display: 'flex', alignItems: 'center', }}> <DatePicker className='w-96 h-10 p-2.5 shadow appearance-none border rounded text-gray-700 leading-tight focus:outline-none focus:shadow-outline' selected={subscriptionDate} dateFormat="dd/MM/yyyy" onChange={(date) => setSubscriptiondate(date)} placeholderText='DD/MM/YYYY' /></div>


                    <div className="bg-gray-200 p-4">Name of the Lead</div>
                    <input name='LeadName' value={cpCommissionhistory.LeadName} onChange={handleOnChangeComHistory} class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Enter Here" />


                    <div className="bg-gray-200 p-4">Solution Type</div>
                    <input name='solutionType' value={cpCommissionhistory.solutionType} onChange={handleOnChangeComHistory} class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Enter Here" />


                    <div className="bg-gray-200 p-4">Commission Amount</div>
                    <input name='commissionAmount' value={cpCommissionhistory.commissionAmount} onChange={handleOnChangeComHistory} class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Enter Here" />

                    <div className="bg-gray-200 p-4">Payment Mode</div>
                    <input name='paymentMode' value={cpCommissionhistory.paymentMode} onChange={handleOnChangeComHistory} class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Enter Here" />
                  </div>
                  <button onClick={SubmitCpCommissionHistory} type="button" style={{ margin: '10px' }} class="text-white bg-gradient-to-r from-green-400 via-green-500 to-green-600 hover:bg-gradient-to-br focus:ring-4 focus:outline-none focus:ring-green-300 dark:focus:ring-green-800 shadow-lg shadow-green-500/50 dark:shadow-lg dark:shadow-green-800/80 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2">Submit</button>
                </div>
              </div>
            )}
            {activecomponent === 4 && (
              <div style={{ height: '80vh', width: "85%", display: 'flex', flexDirection: 'column'}}>
                <div style={{ width: "100%", height: "100vh", display: "flex", flexDirection: 'column', alignItems: 'center' }}>

                  {isPaymentDataAvailable === true ? (

                    <>
                    {paymentDetails.map((detail=>(
                      <>
                      <div className="grid grid-cols-2 gap-4 p-4" style={{ width: '90%',border:'1px solid',marginBottom:"40px" }}>
                      {detail?.paymentMethod === 'Bank Transfer' && (
                        <>
                        <div className="bg-gray-200 p-4">paymentMethod</div>
                          <div className="bg-gray-200 p-4">{detail?.paymentMethod}</div>
                          <div className="bg-gray-200 p-4">accountHolder</div>
                          <div className="bg-gray-200 p-4">{detail?.accountHolder}</div>
                          <div className="bg-gray-200 p-4">accountNumber</div>
                          <div className="bg-gray-200 p-4">{detail?.accountNumber}</div>
                          <div className="bg-gray-200 p-4">bankName</div>
                          <div className="bg-gray-200 p-4">{detail?.bankName}</div>
                          <div className="bg-gray-200 p-4">ifscCode</div>
                          <div className="bg-gray-200 p-4">{detail?.ifscCode}</div>
                          <div className="bg-gray-200 p-4">swiftCode</div>
                          <div className="bg-gray-200 p-4">{detail?.swiftCode}</div>
                          <div className="bg-gray-200 p-4">mobileNumber</div>
                          <div className="bg-gray-200 p-4">{detail?.mobileNumber}</div>
                          <div className="bg-gray-200 p-4">address</div>
                          <div className="bg-gray-200 p-4">{detail?.address}</div>
                        </>
                      )}
                      {detail?.paymentMethod === 'PayPal' && (
                        <>
                          <div className="bg-gray-200 p-4">paymentMethod</div>
                          <div className="bg-gray-200 p-4">{detail?.paymentMethod}</div>
                          <div className="bg-gray-200 p-4">Paypal details</div>
                          <div className="bg-gray-200 p-4">{detail?.paypaldetails}</div>
                        </>
                      )}
                      {
                        detail?.paymentMethod === 'Payment Link' && (
                          <>
                           <div className="bg-gray-200 p-4">paymentMethod</div>
                          <div className="bg-gray-200 p-4">{detail?.paymentMethod}</div>
                          <div className="bg-gray-200 p-4">Paypal details</div>
                          <div className="bg-gray-200 p-4">{detail?.paymentLinkdetails}</div>
                          </>
                        )}


                    </div>
                      </>
                    )))}
                    </>
                  ) : (
                    <div>not data found</div>
                  )}



                </div>
              </div>
            )}
            {activecomponent === 5 && (
              <div style={{ height: '80vh', width: "85%", display: 'flex', flexDirection: 'column' }}>
                <div style={{ width: "100%", height: "70vh", display: "flex", flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
                  <h1 style={{ fontSize: "25px", margin: '10px' }}>Message</h1>
                  <textarea value={message} onChange={(e) => setMessage(e.target.value)} style={{ width: "40rem", height: "140px", border: '1px solid', margin: "10px", padding: "5px", borderRadius: '5px' }} class="form-control" id="exampleFormControlTextarea1" rows="3" placeholder='Type Message'></textarea>
                  <button onClick={handleSubmitMessage} type="button" style={{ margin: '10px' }} class="text-white bg-gradient-to-r from-green-400 via-green-500 to-green-600 hover:bg-gradient-to-br focus:ring-4 focus:outline-none focus:ring-green-300 dark:focus:ring-green-800 shadow-lg shadow-green-500/50 dark:shadow-lg dark:shadow-green-800/80 font-medium rounded-lg text-sm px-5 py-2.5 text-center mr-2 mb-2">Send Message</button>
                </div>
              </div>
            )}
          </div>
        </div>

      </div>
    </>
  );
}

