import React, { useState } from 'react'
import Navbar from '../../components/Navbar'
import Sidebar from '../../components/Sidebar'
import axios from 'axios';

export default function UpdatePayoutDetails() {
  const [selectedItem, setSelectedItem] = useState(1);
  const [banktransferdetail, setBanktransferdetail] = useState({
    accountHolder: '',
    accountNumber: "",
    bankName: '',
    bankAddress: '',
    swiftCode: '',
    ifscCode: "",
    mobileNumber: '',
    address: ''
  })
  const [paypaldetails, setPaypaldetails] = useState('')
  const [paymentLinkdetails, setPaymentlinkdetails] = useState("")



  const items = [
    { id: 1, label: 'Bank Transfer' },
    { id: 2, label: 'PayPal' },
    { id: 3, label: 'Payment Link' },
  ];

  const handleItemClick = (itemId) => {
    setSelectedItem(itemId === selectedItem ? null : itemId);
    setBanktransferdetail({
      accountHolder: '',
      accountNumber: "",
      bankName: '',
      bankAddress: '',
      swiftCode: '',
      ifscCode: "",
      mobileNumber: '',
      address: '',
    })
    setPaymentlinkdetails('')
    setPaypaldetails("")

  };

  let name, value
  const handleChange = (e) => {
    name = e.target.name
    value = e.target.value
    setBanktransferdetail({ ...banktransferdetail, [name]: value })
  }

  const handleBankTransferSubmit = async () => {
    try {
      const newdata = { ...banktransferdetail, paymentLinkdetails, paypaldetails, paymentMethod: "Bank Transfer" }
      const res = await axios.post('/api/paymentdetails', newdata)
      if(res.data.success){
        window.alert('payment data updated')
      }else{
        window.alert(res.data.message)
      }
    } catch (err) {
      console.log(err)
    }
  }
  const handlepaypalSubmit = async () => {
    try {
      const newdata = { ...banktransferdetail, paymentLinkdetails, paypaldetails, paymentMethod: "PayPal" }
      const res = await axios.post('/api/paymentdetails', newdata)
      if(res.data.success){
        window.alert('payment data updated')
      }else{
        window.alert(res.data.message)
      }
    } catch (err) {
      console.log(err)
    }
  }
  const handlepaymentlinkSubmit = async () => {
    try {
      const newdata = { ...banktransferdetail, paymentLinkdetails, paypaldetails, paymentMethod: "Payment Link" }
      const res = await axios.post('/api/paymentdetails', newdata)
      if(res.data.success){
        window.alert('payment data updated')
      }else{
        window.alert(res.data.message)
      }
    } catch (err) {
      console.log(err)
    }
  }


  return (
    <>
      <Navbar />
      <div style={{ display: 'flex' }}>
        <Sidebar />

        <div style={{ display: 'flex', flexDirection: "column", alignItems: 'center', justifyContent: 'center', width: "84%", height: "88vh" }}>
          <div style={{ display: "flex", flexDirection: "row", fontSize: "22px" }}>
            <div>Current Payment Mode : </div>
            <div style={{ marginLeft: '10px', color: 'green' }}>
              {items[selectedItem - 1]?.label}
            </div>
          </div>
          <div style={{ height: '80vh', width: "60%", padding: "5px 10px", borderRadius: "20px", border: "2px solid" }}>
            <h1 style={{ fontSize: '30px' }}>Select any one of them :</h1>
            <div className="p-4">
              <ul className="flex justify-between">
                {items.map((item) => (
                  <li key={item.id} className="flex items-center">
                    <input
                      type="checkbox"
                      className="h-4 w-4 mr-2 mb-2.5"
                      id={`item${item.id}`}
                      checked={item.id === selectedItem}
                      onChange={() => handleItemClick(item.id)}
                    />
                    <label htmlFor={`item${item.id}`} className="cursor-pointer" style={{ fontSize: '17px' }}>
                      {item.label}
                    </label>
                  </li>
                ))}
              </ul>
            </div>
            {selectedItem === 1 && (
              <div style={{ width: '100%', height: "60vh", display: "flex", alignItems: "center", justifyContent: 'center', fontSize: '13px' }}>
                <div class="w-full max-w-sm" >
                  <div class="md:flex md:items-center mb-0">
                    <div class="md:w-1/2">
                      <label class="block text-gray-500 font-bold md:text mb-1 md:mb-0 pr-4" for="inline-full-name" style={{marginBottom:"10px"}}>
                        Account Holder
                      </label>
                    </div>
                    <div class="md:w-2/3">
                      <input name="accountHolder" onChange={handleChange} value={banktransferdetail.accountHolder} class="bg-gray-200 appearance-none border-2 border-gray-200 rounded w-full h-8 py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-purple-500" id="inline-full-name" type="text" />
                    </div>
                  </div>
                  <div class="md:flex md:items-center mb-0">
                    <div class="md:w-1/2">
                      <label class="block text-gray-500 font-bold md:text mb-1 md:mb-0 pr-4" for="inline-password" style={{marginBottom:"10px"}}>
                        Account Number
                      </label>
                    </div>
                    <div class="md:w-2/3">
                      <input name="accountNumber" onChange={handleChange} value={banktransferdetail.accountNumber} class="bg-gray-200 appearance-none border-2 border-gray-200 rounded w-full h-8 py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-purple-500" id="inline-accountnumber" type="text" />
                    </div>
                  </div>
                  <div class="md:flex md:items-center mb-0">
                    <div class="md:w-1/2">
                      <label class="block text-gray-500 font-bold md:text mb-1 md:mb-0 pr-4" for="inline-password" style={{marginBottom:"10px"}}>
                        Bank Name
                      </label>
                    </div>
                    <div class="md:w-2/3">
                      <input name="bankName" onChange={handleChange} value={banktransferdetail.bankName} class="bg-gray-200 appearance-none border-2 border-gray-200 rounded w-full h-8 py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-purple-500" id="inline-password" type="text" />
                    </div>
                  </div>
                  <div class="md:flex md:items-center mb-0">
                    <div class="md:w-1/2">
                      <label class="block text-gray-500 font-bold md:text mb-1 md:mb-0 pr-4" for="inline-password" style={{marginBottom:"10px"}}>
                        Bank Address
                      </label>
                    </div>
                    <div class="md:w-2/3">
                      <input name="bankAddress" onChange={handleChange} value={banktransferdetail.bankAddress} class="bg-gray-200 appearance-none border-2 border-gray-200 rounded w-full h-8 py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-purple-500" id="inline-password" type="text" />
                    </div>
                  </div>
                  <div class="md:flex md:items-center mb-0">
                    <div class="md:w-1/2">
                      <label class="block text-gray-500 font-bold md:text mb-1 md:mb-0 pr-4" for="inline-password" style={{marginBottom:"10px"}}>
                        SWIFT Code
                      </label>
                    </div>
                    <div class="md:w-2/3">
                      <input name="swiftCode" onChange={handleChange} value={banktransferdetail.swiftCode} class="bg-gray-200 appearance-none border-2 border-gray-200 rounded w-full h-8 py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-purple-500" id="inline-password" type="text" />
                    </div>
                  </div>
                  <div class="md:flex md:items-center mb-0">
                    <div class="md:w-1/2">
                      <label class="block text-gray-500 font-bold md:text mb-1 md:mb-0 pr-4" for="inline-password" style={{marginBottom:"10px"}}>
                        IFSC Code
                      </label>
                    </div>
                    <div class="md:w-2/3">
                      <input name="ifscCode" onChange={handleChange} value={banktransferdetail.ifscCode} class="bg-gray-200 appearance-none border-2 border-gray-200 rounded w-full h-8 py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-purple-500" id="inline-password" type="text" />
                    </div>
                  </div>
                  <div class="md:flex md:items-center mb-0">
                    <div class="md:w-1/2">
                      <label class="block text-gray-500 font-bold md:text mb-1 md:mb-0 pr-4" for="inline-password" style={{marginBottom:"10px"}}>
                        Mobile Number
                      </label>
                    </div>
                    <div class="md:w-2/3">
                      <input name="mobileNumber" onChange={handleChange} value={banktransferdetail.mobileNumber} class="bg-gray-200 appearance-none border-2 border-gray-200 rounded w-full h-8 py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-purple-500" id="inline-password" type="text" />
                    </div>
                  </div>
                  <div class="md:flex md:items-center mb-0">
                    <div class="md:w-1/2">
                      <label class="block text-gray-500 font-bold md:text mb-1 md:mb-0 pr-4" for="inline-password" style={{marginBottom:"10px"}}>
                        Address
                      </label>
                    </div>
                    <div class="md:w-2/3">
                      <input name="address" onChange={handleChange} value={banktransferdetail.address} class="bg-gray-200 appearance-none border-2 border-gray-200 rounded w-full h-8 py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-purple-500" id="inline-password"
                        type="text" />
                    </div>
                  </div>
                  <div class="md:flex md:items-center">
                    <div class="md:w-1/3"></div>
                    <div class="md:w-2/3">
                      <button class="shadow bg-white hover:bg-purple-400 focus:shadow-outline focus:outline-none text-black border border-gray-900 font-bold py-2 px-4 rounded" type="button"
                        onClick={handleBankTransferSubmit}
                      >
                        Submit
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            )}
            {selectedItem === 2 && (
              <div style={{ width: '100%', height: "65vh", display: "flex", alignItems: "center", justifyContent: 'center', fontSize: '13px' }}>
                <div class="w-full max-w-sm" >
                  <div class="md:flex md:items-center mb-8">
                    <div class="md:w-1/2">
                      <label class="block text-gray-500 font-bold md:text mb-1 md:mb-0 pr-4" for="inline-full-name" style={{marginBottom:"10px"}}>
                        Enter PayPal Details
                      </label>
                    </div>
                    <div class="md:w-2/3">
                      <input name="paypaldetails" onChange={(e) => { setPaypaldetails(e.target.value) }} value={paypaldetails} class="bg-gray-200 appearance-none border-2 border-gray-200 rounded w-full h-8 py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-purple-500" id="inline-full-name" type="text" />
                    </div>
                  </div>
                  <div class="md:flex md:items-center">
                    <div class="md:w-1/3"></div>
                    <div class="md:w-2/3">
                      <button class="shadow bg-white hover:bg-purple-400 focus:shadow-outline focus:outline-none text-black border border-gray-900 font-bold py-2 px-4 rounded" type="button"
                        onClick={handlepaypalSubmit}
                      >
                        Submit
                      </button>
                    </div>
                  </div>
                </div>
              </div>

            )}
            {selectedItem === 3 && (
              <div style={{ width: '100%', height: "65vh", display: "flex", alignItems: "center", justifyContent: 'center', fontSize: '13px' }}>
                <div class="w-full max-w-sm" >
                  <div class="md:flex md:items-center mb-8">
                    <div class="md:w-1/2">
                      <label class="block text-gray-500 font-bold md:text mb-1 md:mb-0 pr-4" for="inline-full-name" style={{marginBottom:"10px"}}>
                        Enter the Payment Link
                      </label>
                    </div>
                    <div class="md:w-2/3">
                      <input name="paymentlink" onChange={(e) => { setPaymentlinkdetails(e.target.value) }} value={paymentLinkdetails} class="bg-gray-200 appearance-none border-2 border-gray-200 rounded w-full h-8 py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-purple-500" id="inline-full-name" type="text" />
                    </div>
                  </div>
                  <div class="md:flex md:items-center">
                    <div class="md:w-1/3"></div>
                    <div class="md:w-2/3">
                      <button class="shadow bg-white hover:bg-purple-400 focus:shadow-outline focus:outline-none text-black border border-gray-900 font-bold py-2 px-4 rounded" type="button"
                        onClick={handlepaymentlinkSubmit}
                      >
                        Submit
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            )}


          </div>
        </div>

      </div >

    </>
  )
}
