import React, { useState } from 'react';
import './Navbar.css';
import newImage from '../Component/TIMEIMAGE/new.jpg';
import Subscribe from './Subscribe';
import DashboardLoginONE from './DashboardLoginONE.jsx';
import { Link, useNavigate } from 'react-router-dom';

const RegisterPopup = () => {
  return (
    <div className="register-popup-content">
     
      <Link to='/userregister'><button className='register-popup-content112'>Register</button></Link>
    </div>
  );
};

const PDFs = {
  India: require('../Component/pdfs/India.pdf'),
  Singapore: require('../Component/pdfs/Singapore.pdf'),
  Thailand: require('../Component/pdfs/Thailand.pdf'),
  Australia: require('../Component/pdfs/Australia.pdf'),
  NewZealand: require('../Component/pdfs/NewZealand.pdf'),
  HongKong: require('../Component/pdfs/HongKong.pdf'),
  UAE: require('../Component/pdfs/UAE.pdf'),
  Europe: require('../Component/pdfs/Europe, United Kingdom.pdf'),
  Canada: require('../Component/pdfs/Canada.pdf'),
  USA: require('../Component/pdfs/U.S.A.pdf'),
};

const Navbar = () => {
  const [showPopup, setShowPopup] = useState(false);
  const [selectedCountry, setSelectedCountry] = useState('');
  const [downloading, setDownloading] = useState(false);
  const [downloadCompleted, setDownloadCompleted] = useState(false);
  const [showSubscribe, setShowSubscribe] = useState(false);
  const [showDashboardLoginONE, setShowDashboardLoginONE] = useState(false);
  const [showRegisterButton, setShowRegisterButton] = useState(false);
  const navigate = useNavigate()

  const countries = [
    { name: 'Asia Pacific', addresses: ['India', 'Singapore', 'Thailand', 'Australia', 'NewZealand', 'HongKong', 'UAE'] },
    { name: 'Europe', addresses: ['Europe', 'UnitedKingdom'] },
    { name: 'North America', addresses: ['Canada', 'USA'] },
  ];

  const handleSubscribeClick = () => {
    setShowSubscribe(!showSubscribe);
    setShowDashboardLoginONE(false);
    setShowPopup(false);
  };

  const handleDashboardLoginONEClick = () => {
    setShowDashboardLoginONE(true);
    setShowSubscribe(false);
    setShowPopup(false);
  };

  const handleDownloadClick = () => {
    setShowPopup(!showPopup);
    setShowSubscribe(false);
    setShowDashboardLoginONE(false);
  };

  const handleCountryClick = (countryName) => {
    setSelectedCountry(countryName);
    setDownloading(true);
    setDownloadCompleted(false);
  };

  const handleCloseAddressPopup = () => {
    setSelectedCountry('');
    setDownloadCompleted(false);
  };

  const handleDownloadPDF = (pdfURL) => {
    const link = document.createElement('a');
    link.href = pdfURL;
    link.download = 'brochure.pdf';
    link.click();
    setDownloadCompleted(true);
  };

  const handleRegisterClick = () => {
    setShowRegisterButton(!showRegisterButton);
    setShowSubscribe(false);
    setShowDashboardLoginONE(false);
    setShowPopup(false); 
  };

  

  return (
    <div>
      <nav className="navbar">
        <div className="navbar-icon">
          <img src={newImage} alt="Wrench Icon" />
        </div>
        <ul className="navbar-items0">
          <div className="navbar-item box0" onClick={handleSubscribeClick}>
            Subscribe Now
          </div>
          <div className="navbar-item box0" onClick={handleDashboardLoginONEClick}>
            Dashboard Login
          </div>
          <div className="navbar-item box0" onClick={handleDownloadClick}>
            Download Brochure
            {showPopup && (
              <div className="popup1">
                {countries.map((country, index) => (
                  <div
                    className="popup-item"
                    key={index}
                    onClick={() => handleCountryClick(country.name)}
                  >
                    {country.name}
                  </div>
                ))}
                <button className="popup-close-btn" onClick={() => setShowPopup(false)}>
                  Close
                </button>
              </div>
            )}
          </div>
          <div className="navbar-item box0">Blogs</div>
          <div className="navbar-item box0" onClick={handleRegisterClick}>
            Channal Partner
          </div>
          {showRegisterButton && <RegisterPopup />}
        </ul>
      </nav>
      {showSubscribe && <Subscribe />}
      {showDashboardLoginONE && <DashboardLoginONE />}
      {selectedCountry && (
        <div className="popup-address">
          <button className="address-close-btn" onClick={handleCloseAddressPopup}>
            Close
          </button>
          <ul className="pdf-list">
            {countries
              .find((country) => country.name === selectedCountry)
              .addresses.map((address, index) => (
                <li key={index}>
                  <div className="pdf-box">
                    <a href={PDFs[address]} target="_blank" rel="noopener noreferrer">
                      {address}
                    </a>
                    <button onClick={() => handleDownloadPDF(PDFs[address])}>
                      Download
                    </button>
                  </div>
                </li>
              ))}
          </ul>
        </div>
      )}
    </div>
  );
};

export default Navbar;
