import React, { useState } from 'react';
import Select from 'react-select';
import './CorporateOffices.css';

import Navbar from './Navbar';
import Footer from './Footer'; // Import the Footer component

const CorporateOffices = () => {
  const [selectedRegion, setSelectedRegion] = useState(null);
  const [selectedAddress, setSelectedAddress] = useState(null);

  const regions = [
    { value: 'Asia Pacific', label: 'Asia Pacific' },
    { value: 'Europe', label: 'Europe' },
    { value: 'NorthAmerica', label: 'North America' },
  ];

  const addresses = {
    // Define your addresses here
  };

  const selectRegion = (selectedOption) => {
    setSelectedRegion(selectedOption);
    setSelectedAddress(null);
  };

  const selectAddress = (selectedOption) => {
    setSelectedAddress(selectedOption);
  };

  const getOptionsForSelectedRegion = () => {
    if (selectedRegion) {
      const region = addresses[selectedRegion.value];
      if (region) {
        const addressGroups = Object.keys(region);
        const options = addressGroups.map((group) => {
          const addressesInGroup = region[group];
          const addressOptions = Object.keys(addressesInGroup).map((address) => ({
            value: address,
            label: address,
          }));
          return {
            label: group,
            options: addressOptions,
          };
        });
        return options;
      }
    }
    return [];
  };

  return (
    <div>
      <Navbar />
      <div className="office-container">
        <Select
          options={regions}
          value={selectedRegion}
          onChange={selectRegion}
          placeholder="Select a Region"
        />

        {selectedAddress && (
          <div className="selected-address">
            Selected Address: {selectedAddress.label}
          </div>
        )}
      </div>
      <Footer />
    </div>
  );
};

export default CorporateOffices;


