import React from 'react';
import './Subscribe.css';

function SubscriptionProcess() {
  return (
    <div className="subscription-container">
      <div className="left-section">
        <div className='nickleft'></div>
        <div className='red'>Before Subscribing</div><br></br>
        <div className="step-box">
        <span className="text-after-icon"><span className="step-text">Step 1:</span> Visit  <a href="/YourFile"  className="external-link">
  www.fixtechcare.com
</a></span>
        </div>
        <div className="step-box">
          <span className="text-after-icon"><span className="step-text">Step 2:</span> Go Through the platform</span>
        </div>
        <div className="step-box">
          <span className="text-after-icon"><span className="step-text">Step 3:</span> Fill the Subscription Form</span>
        </div>
        <div className="step-box">
          <span className="text-after-icon"><span className="step-text">Step 4:</span> Pay from multiple payment options</span>
        </div>
        <div className="step-box">
          <span className="text-after-icon"><span className="step-text">Step 5:</span> Check email</span>
        </div>
        <div className="step-box">
          <span className="text-after-icon"><span className="step-text">Step 6:</span> Your account is activated now</span>
        </div>
      </div>


      <div className="right-section">
        <div className='nickright'></div>
        <div className='red'>After Subscribing</div><br></br>
        <div className="step-box">
          <span className="text-after-icon"><span className="step-text">Step 1:</span> Login to the dashboard</span>
        </div>
        <div className="step-box">
          <span className="text-after-icon"><span className="step-text">Step 2:</span> Go through all features</span>
        </div>
        <div className="step-box">
          <span className="text-after-icon"><span className="step-text">Step 3:</span> Service Request</span>
        </div>
        <div className="step-box">
          <span className="text-after-icon"><span className="step-text">Step 4:</span> Track Engineer Live</span>
        </div>
        <div className="step-box">
          <span className="text-after-icon"><span className="step-text">Step 5:</span> Technician will complete their task</span>
        </div>
        <div className="step-box">
          <span className="text-after-icon"><span className="step-text">Step 6:</span> Fill the service feedback form</span>
        </div>
      </div>
    </div>
  );
}

export default SubscriptionProcess;
