import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './YourFile.css';
import newImage from '../Component/TIMEIMAGE/new21.jpg';
import actVideo from '../Component/TIMEIMAGE/act.mp4';
import Footer from './Footer';

const YourFile = () => {
  const [isPlaying, setIsPlaying] = useState(true);

  return (
    <div className="container">
      <div className="icon2">
        <img src={newImage} alt="Wrench Icon" />
      </div>
<br></br>
<br></br>
      <div className="background-video" >
        <video
          autoPlay={isPlaying}
          loop
          muted={true}
          className="background-video-content"
        >
          <source src={actVideo} type="video/mp4" />
          Your browser does not support the video tag.
        </video>
      </div>

      <div className="right-content">
        <div className="right-row">
          {/* Your video content */}
        </div>
        <div className="two-div-container">
          <Link to="/fixtech">
            <div className="left-divleft">Individual</div>
          </Link>
          <div className="gap"></div> {/* Add a gap between the elements */}
          <Link to="/web">
            <div className="right-divright">Enterprise</div>
          </Link>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default YourFile;

