import React from 'react';
import './CookiesPolicy.css';
import { Link } from 'react-router-dom'; // Import Link from react-router-dom

import Navbar from './Navbar';
import Footer from './Footer';

const  CookiesPolicy= () => {
  return (
    <div className="containervv">
      <Navbar />
    <div className="cookies-policy">
      <h1 className="policy-titleyy">Cookies Policy</h1>
      <p className="last-updatedyy">Last updated: October 06, 2023</p>
      <p className="policy-textyy">
        This Cookies Policy explains what Cookies are and how We use them. You should read this policy so You can understand what type of cookies We use, or the information We collect using Cookies and how that information is used.
      </p>
      <p className="policy-textyy">
        Cookies do not typically contain any information that personally identifies a user, but personal information that we store about You may be linked to the information stored in and obtained from Cookies. For further information on how We use, store, and keep your personal data secure, see our Privacy Policy.
      </p>
      <p className="policy-textyy">
        We do not store sensitive personal information, such as mailing addresses, account passwords, etc. in the Cookies We use.
      </p>
      <h2 className="section-titleyy">Interpretation and Definitions</h2>
      <h4 className="subsection-titleyy">Interpretation</h4>
      <p className="policy-textyy">
        The words of which the initial letter is capitalized have meanings defined under the following conditions. The following definitions shall have the same meaning regardless of whether they appear in singular or in plural.
      </p>
      <h4 className="subsection-titleyy">Definitions</h4>
      <p className="policy-textyy">For the purposes of this Cookies Policy:</p>
      <ul className="term-listyy">
        <li>
          <strong className="term-nameyy">Company</strong> (referred to as either "the Company", "We", "Us", or "Our" in this Cookies Policy) refers to FixTech Facility Services, Mumbai.
        </li>
        <li>
          <strong className="term-nameyy">Cookies</strong> means small files that are placed on Your computer, mobile device, or any other device by a website, containing details of your browsing history on that website among its many uses.
        </li>
        <li>
          <strong className="term-nameyy">Website</strong> refers to FixTech Care, accessible from <Link to="/YourFile" className="external-link">www.fixtechcare.com</Link>
        </li>
        <li>
          <strong className="term-nameyy">You</strong> means the individual accessing or using the Website, or a company, or any legal entity on behalf of which such individual is accessing or using the Website, as applicable.
        </li>
      </ul>
      <h2 className="section-titleyy">The use of the Cookies</h2>
      <h4 className="subsection-titleyy">Type of Cookies We Use</h4>
      <p className="policy-textyy">
        Cookies can be "Persistent" or "Session" Cookies. Persistent Cookies remain on your personal computer or mobile device when You go offline, while Session Cookies are deleted as soon as You close your web browser.
      </p>
      <p className="policy-textyy">
        We use both session and persistent Cookies for the purposes set out below:
      </p>
      <ul className="term-listyy">
        <li>
          <p className="term-nameyy"><strong>Necessary / Essential Cookies</strong></p>
          <p className="policy-textyy">Type: Session Cookies</p>
          <p className="policy-textyy">Administered by: Us</p>
          <p className="policy-textyy">
            Purpose: These Cookies are essential to provide You with services available through the Website and to enable You to use some of its features. They help to authenticate users and prevent fraudulent use of user accounts. Without these Cookies, the services that You have asked for cannot be provided, and We only use these Cookies to provide You with those services.
          </p>
        </li>
        <li>
          <p className="term-nameyy"><strong>Functionality Cookies</strong></p>
          <p className="policy-textyy">Type: Persistent Cookies</p>
          <p className="policy-textyy">Administered by: Us</p>
          <p className="policy-textyy">
            Purpose: These Cookies allow us to remember choices You make when You use the Website, such as remembering your login details or language preference. The purpose of these Cookies is to provide You with a more personal experience and to avoid You having to re-enter your preferences every time You use the Website.
          </p>
        </li>
      </ul>
      <h4 className="subsection-titleyy">Your Choices Regarding Cookies</h4>
      <p className="policy-textyy">
        If You prefer to avoid the use of Cookies on the Website, first You must disable the use of Cookies in your browser and then delete the Cookies saved in your browser associated with this website. You may use this option for preventing the use of Cookies at any time.
      </p>
      <p className="policy-textyy">If You do not accept Our Cookies, You may experience some inconvenience in your use of the Website, and some features may not function properly.</p>
      <p className="policy-textyy">If You'd like to delete Cookies or instruct your web browser to delete or refuse Cookies, please visit the help pages of your web browser.</p>
      <ul className="term-listyy">
        <li>
          For the Chrome web browser, please visit this page from Google: <a className="external-linknnn" href="https://support.google.com/accounts/answer/32050" rel="external nofollow noopener" target="_blank">https://support.google.com/accounts/answer/32050</a>
        </li>
        <li>
          For the Internet Explorer web browser, please visit this page from Microsoft: <a className="external-linknnn" href="http://support.microsoft.com/kb/278835" rel="external nofollow noopener" target="_blank">http://support.microsoft.com/kb/278835</a>
        </li>
        <li>
          For the Firefox web browser, please visit this page from Mozilla: <a className="external-linknnn" href="https://support.mozilla.org/en-US/kb/delete-cookies-remove-info-websites-stored" rel="external nofollow noopener" target="_blank">https://support.mozilla.org/en-US/kb/delete-cookies-remove-info-websites-stored</a>
        </li>
        <li>
          For the Safari web browser, please visit this page from Apple: <a className="external-linknnn" href="https://support.apple.com/guide/safari/manage-cookies-and-website-data-sfri11471/mac" rel="external nofollow noopener" target="_blank">https://support.apple.com/guide/safari/manage-cookies-and-website-data-sfri11471/mac</a>
        </li>
      </ul>
      <h4 className="subsection-titleyy">More Information about Cookies</h4>
      <p className="policy-textyy"><a className="external-link3" href="https://example.com" target="_blank">Cookies: What Do They Do?</a></p>
      <h4 className="subsection-titleyy">Contact Us</h4>
      <p className="policy-textyy">If you have any questions about this Cookies Policy, You can contact us:</p>
      <ul className="term-listyy">
        <li>By email: <a className="contact-emailyy" href="mailto:feedback@fixtechcare.com">feedback@fixtechcare.com</a></li>
      </ul>
      </div>
      <Footer />
    </div>
  );
}

export default CookiesPolicy;
