import React, { useState } from 'react';
import './Management.css';
import Navbar from './Navbar';
import Footer from './Footer';

const Management = () => {
  const [currentRoleIndex, setCurrentRoleIndex] = useState(0);

  const managementRoles = ['Founder', 'Asia Pacific', 'Europe', 'N&S America'];

  const handleMoveForward = () => {
    if (currentRoleIndex < managementRoles.length - 1) {
      setCurrentRoleIndex(currentRoleIndex + 1);
    }
  };

  const handleMoveBack = () => {
    if (currentRoleIndex > 0) {
      setCurrentRoleIndex(currentRoleIndex - 1);
    }
  };

  return (
    <div className="containervv">
      <Navbar />
      <div className="management-container">
        <div className="button-container0n0">
          <button onClick={handleMoveBack} className='xxx'>
            <svg xmlns="http://www.w3.org/2000/svg" width="50" height="50" style={{marginLeft:"auto"}}>
              <path d="M10 20A10 10 0 1 0 0 10a10 10 0 0 0 10 10zM8.711 4.3l5.7 5.766L8.7 15.711l-1.4-1.422 4.289-4.242-4.3-4.347z" />
            </svg>
          </button>
        </div>
        <div className="management-box0n0n">
          {managementRoles[currentRoleIndex]}
        </div>
        <div className="button-container343">
          <button onClick={handleMoveForward} className='xxx'>
            <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20">
              <path d="M10 20A10 10 0 1 0 0 10a10 10 0 0 0 10 10zm1.289-15.7 1.422 1.4-4.3 4.344 4.289 4.245-1.4 1.422-5.714-5.648z" />
            </svg>
          </button>
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default Management;
