import React from 'react'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'

export default function Sidebar() {
    const {user} = useSelector(state=>state.user)

    const menu = [
        {
            icon: 'bi bi-house',
            name: 'Home',
            path: '/userhome'
        },
        {
            icon: 'bi bi-person',
            name: 'Account Details',
            path: "/accountdetails"
        },
        {
            icon: 'bi bi-arrow-clockwise',
            name: 'Referral History',
            path: '/referralhistory'
        },
        {
            icon: 'bi bi-cash-stack',
            name: 'Commissions History',
            path: '/commissonshistory'
        },
        {
            icon: 'bi bi-credit-card',
            name: 'Update Payout Details',
            path: '/updatepayoutdetails'
        },
        {
            icon: 'bi bi-headset',
            name: 'Support Account',
            path: '/supportaccount'
        },
        {
            icon: 'bi bi-question-circle',
            name: 'FAQ',
            path: '/faq'
        },

    ]

    return (
        <div style={{ width: "16%", height: "88vh", backgroundColor: "#dad1e0",borderRadius:"10px" }}>
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', flexDirection: 'column', marginBottom: '10px',borderBottom:"1px solid white" }}>
                <img src='https://img.freepik.com/free-vector/businessman-character-avatar-isolated_24877-60111.jpg?w=2000' style={{ width: "80px", height: '80px', borderRadius: "50%", marginTop: '10px' }} />
                <h3 style={{ marginTop: "5px", marginBottom: '5px', fontSize: '20px' }}>welcome {user?.firstName}</h3>
            </div>
            {
                menu.map((element) => {
                    return (
                        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center',flexDirection:"column",  marginBottom: '8px' ,borderBottom:"1px solid white"}}>
                            <i class={element.icon} style={{color:"black",fontSize:"20px"}}></i>
                            <Link to={element.path} style={{ marginTop: "1px", marginBottom: '5px', fontSize: '15px',textDecoration:'none',color:'black' }} > {element.name} </Link>
                        </div>
                    )
                })
            }
        </div>
    )
}
