import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import axios from 'axios'
// import { useDispatch } from 'react-redux'
// import { showLoading,hideLoading } from '../redux/features/alertSlice'

export default function AdminLogin() {
    const navigate = useNavigate()
    // const dispatch = useDispatch()

    const [user, setUser] = useState({
        email: "",
        password: ""
    })
    let name, value
    const handleOnChange = (event) => {
        name = event.target.name
        value = event.target.value
        setUser({ ...user, [name]: value })
    }

    const handleOnClick = async (event) => {
        event.preventDefault()
        try {
            // dispatch(showLoading())
            // console.log(user)
            const res = await axios.post('/api/adminlogin',user)
            // dispatch(hideLoading())
            if (res.status === 200) {
                window.alert("successfully login")
                navigate("/adminpage")
            }
            else{
                // dispatch(hideLoading())
                window.alert('email already Exist')
            }
        } catch (err) {
            console.log(err)
        }
    }
    return (
        <div className="relative flex flex-col justify-center min-h-screen overflow-hidden">
            <div className="w-full p-6 m-auto bg-gray-100 rounded-md shadow-md lg:max-w-xl border-solid border-2 border-black">
                <h1 className="text-3xl font-semibold text-center text-purple-700 underline">
                    Admin Login
                </h1>
                <div className="mt-6">
                    <div className="mb-2">
                        <label
                            for="referralid"
                            className="block text-xl font-semibold text-gray-800"
                        >
                            UserName<span className='required'>*</span>
                        </label>
                        <input
                            type="text"
                            style={{width:'100%'}}
                            className="block w-full px-4 py-2 mt-2 bg-white border rounded-md focus:border-purple-400 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
                            name='email' value={user.email} onChange={handleOnChange}
                        />
                    </div>
                    <div className="mb-2">
                        <label
                            for="referralid"
                            className="block text-xl font-semibold text-gray-800"
                        >
                            Password<span className='required'>*</span>
                        </label>
                        <input
                            type="text"
                            style={{width:"100%"}}
                            className="block w-full px-4 py-2 mt-2 bg-white border rounded-md focus:border-purple-400 focus:ring-purple-300 focus:outline-none focus:ring focus:ring-opacity-40"
                            name='password' value={user.password} onChange={handleOnChange}
                        />
                    </div>
                    <div class="flex items-center mb-4">
                        <input id="default-checkbox" type="checkbox" value="" class="w-4 h-4 text-blue-600 bg-gray-100 border-gray-300 rounded focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600" />
                        <label for="default-checkbox" class="ml-2 text-sm font-medium ">Remember Me</label>
                    </div>

                    <div className="mt-6">
                        <button className="w-full px-4 py-2 tracking-wide text-white transition-colors duration-200 transform bg-purple-700 rounded-md hover:bg-purple-600 focus:outline-none focus:bg-purple-600"
                        onClick={handleOnClick}
                        >
                            Login
                        </button>
                    </div>
                </div>
                <div style={{marginTop:"20px"}}>
                <a href='/login' style={{color:"blue"}}>Forgot Password ?</a>
                </div>
                
            </div>
        </div>
    )
}