import React, { useState } from 'react'

export default function Notifications({notifications,seenNotifications}) {
    const [activeComponent,setActiveComponent] = useState(1)
    return (
        <>
        <div style={{ width: '25%', height: "87vh", backgroundColor: "#f1bff1", position: "absolute", right: "0", margin: "3px",overflowY:"scroll",borderTopLeftRadius:'10px',borderBottomLeftRadius:'10px'}} >
            <h1 style={{ fontSize: '20px', margin: "5px" }}>Notifications</h1>
            <div style={{height:"5vh",width:'100%',display:'flex',alignItems:"center",justifyContent:'space-around',marginBottom:"40px"}}>
                <h1 onClick={()=>{setActiveComponent(1)}} style={{backgroundColor:`${activeComponent===1?'rgb(225 145 72)':'#f1bff1'}`,height:"5vh",width:"50%",display:"flex",alignItems:'center',justifyContent:"center",cursor:"pointer",border:"0.5px solid",borderRadius:"10px"}}>New</h1>
                <h1 onClick={()=>{setActiveComponent(2)}} style={{backgroundColor:`${activeComponent===2?'rgb(225 145 72)':'#f1bff1'}`,height:"5vh",width:"50%",display:"flex",alignItems:'center',justifyContent:"center",cursor:"pointer",border:'0.5px solid',borderRadius:"10px"}}>All</h1>
            </div>
            { activeComponent===1 && notifications.map((message,index)=>(                
                <div key={index} style={{ width: "100%", backgroundColor: "rgb(148 73 232)", borderBottom: '1px solid', padding: "5px",marginBottom:"10px",color:"white",borderRadius:"7px" }}>
                <div style={{ width: "100%", height: "5vh", display: 'flex', alignItems: "center", justifyContent: "space-between" }}>
                    <h2 style={{ fontSize: "16px", fontWeight: "500" }}>Admin</h2>
                </div>
                <p style={{fontSize:"14px"}}>{message}</p>
            </div>
            ))} 
            { activeComponent===2 && seenNotifications.map((message,index)=>(                
                <div key={index} style={{ width: "100%", backgroundColor: "rgb(148 73 232)", borderBottom: '1px solid', padding: "5px",marginBottom:"10px",color:"white",borderRadius:"7px" }}>
                <div style={{ width: "100%", height: "5vh", display: 'flex', alignItems: "center", justifyContent: "space-between" }}>
                    <h2 style={{ fontSize: "16px", fontWeight: "500" }}>Admin</h2>
                </div>
                <p style={{fontSize:"14px"}}>{message}</p>
            </div>
            ))} 
        </div>
        </>
    )
}
