import React, { useState } from 'react';
import './SalesContact.css';

import Navbar from './Navbar';
import Footer from './Footer';

const SalesContact = () => {
  const [continent, setContinent] = useState(null);
  const [country, setCountry] = useState(null);
  const [person, setPerson] = useState(null);
  const [isPopUpVisible, setPopUpVisible] = useState(false);

  const data = {
    'ASIA PACIFIC': {
      name: 'Asia Sales Team',
      countries: {
        India: {
          name: 'John Doe',
          phone: '123-456-7890',
        },
        Singapore: {
          name: 'Jane Smith',
          phone: '987-654-3210',
        },
        Thailand: {
          name: 'Tom Johnson',
          phone: '555-123-4567',
        },
        Australia: {
          name: 'Alice Brown',
          phone: '111-222-3333',
        },
        NewZealand: {
          name: 'Bob Wilson',
          phone: '444-555-6666',
        },
        HongKong: {
          name: 'Eve Lee',
          phone: '777-888-9999',
        },
        UAE: {
          name: 'David Miller',
          phone: '333-222-1111',
        },
      },
    },
    EUROPE: {
      name: 'Europe Sales Team',
      countries: {
        'United Kingdom': {
          name: 'William Turner',
          phone: '999-888-7777',
        },
        Ireland: {
          name: 'Grace Murphy',
          phone: '777-666-5555',
        },
        Netherlands: {
          name: 'Jan de Vries',
          phone: '123-456-7890',
        },
        Italy: {
          name: 'Marco Rossi',
          phone: '321-654-9876',
        },
        Hungary: {
          name: 'Anna Kovcs',
          phone: '555-888-2222',
        },
        Switzerland: {
          name: 'Luis Mller',
          phone: '999-777-1111',
        },
        Germany: {
          name: 'Heinrich Schmidt',
          phone: '444-333-2222',
        },
        Poland: {
          name: 'Anna Kowalska',
          phone: '777-555-3333',
        },
        Sweden: {
          name: 'Erik Andersson',
          phone: '888-555-1111',
        },
        Denmark: {
          name: 'Lars Nielsen',
          phone: '666-999-4444',
        },
        Belgium: {
          name: 'Sophie Dupont',
          phone: '222-444-6666',
        },
        France: {
          name: 'Marie Dubois',
          phone: '111-333-5555',
        },
        Spain: {
          name: 'Carlos Snchez',
          phone: '444-777-9999',
        },
        Portugal: {
          name: 'Miguel Santos',
          phone: '333-888-2222',
        },
      },
    },
   'NORTH AMERICA': {
      name: 'North America Sales Team',
      countries: {
        Canada: {
          name: 'Michael Johnson',
          phone: '555-444-3333',
        },
        US: {
          name: 'Mary Davis',
          phone: '333-222-1111',
        },
      },
    },
  };

  const handleContinentClick = (continentName) => {
    setContinent(continentName);
    setCountry(null);
    setPerson(null);
    setPopUpVisible(false);
  };

  const handleCountryClick = (countryName) => {
    setCountry(countryName);
    setPerson(data[continent].countries[countryName]);
    setPopUpVisible(true);
  };

  return (
    <div className="sales-contact-container">
      <Navbar />
      <div className="continent-list">
        {Object.keys(data).map((continentName) => (
          <button key={continentName} onClick={() => handleContinentClick(continentName)}>
            {continentName}
          </button>
        ))}
      </div>
      <div className="country-list">
        {continent && (
          <div>
            {Object.keys(data[continent].countries).map((countryName) => (
              <button key={countryName} onClick={() => handleCountryClick(countryName)}>
                {countryName}
              </button>
            ))}
          </div>
        )}
      </div>
      <div className={`pop-up ${isPopUpVisible ? 'visible' : ''}`}>
        {country && person && (
          <div className="person-info">
            <div>Person Name: {person.name}</div>
            <div>Phone Number: {person.phone}</div>
          </div>
        )}
      </div>
      <Footer />
    </div>
  );
};

export default SalesContact;
